/**
 * 표준 읽기 시간 //TODO: etcTxt 번호 변경 될수있음
 * @param {*} data
 * @returns
 */
export const getEtcInfo = (data) => {
    let result = {};
    console.log(data.content.problem.headerBookCd);
    try {
        if (data.content.property != null) {
            result = {
                kread:
                    data.content.property.header.etcTxt03 === null
                        ? 0
                        : data.content.property.header.etcTxt03,
                title:
                    data.content.property.header.etcTxt01 === null
                        ? ''
                        : data.content.property.header.etcTxt01,
                writer:
                    data.content.property.header.etcTxt02 === null
                        ? ''
                        : data.content.property.header.etcTxt02,
                etcCd01:
                    data.content.property.header.etcCd01 == null
                        ? 0
                        : data.content.property.header.etcCd01,
                etcCd01Nm:
                    data.content.property.header.etcCd01 == null
                        ? ''
                        : headerEtcCd01[data.content.property.header.etcCd01],
                etcCd02:
                    data.content.property.header.etcCd02 == null
                        ? 0
                        : data.content.property.header.etcCd01,
                etcCd02Nm:
                    data.content.property.header.etcCd02 == null
                        ? ''
                        : headerEtcCd02[data.content.property.header.etcCd02],
                bookNm:
                    data.content.property.problem.bookNm == null
                        ? ''
                        : data.content.property.problem.bookNm,
                solveTime:
                    data.content.property.problem.solveTime == null
                        ? 0
                        : data.content.property.problem.solveTime,
                analysis:
                    data.content.property.analysis.analysisNm == null
                        ? ''
                        : data.content.property.analysis.analysisNm,
                difficultCd:
                    data.content.property.analysis.difficultCd == null
                        ? '03'
                        : data.content.property.analysis.difficultCd,
                difficultNm:
                    data.content.property.analysis.difficultCd == null
                        ? '03'
                        : difficult[data.content.property.analysis.difficultCd]
                              ?.label,
            };
        }
    } catch (error) {
        console.log(error);
    }
    return result;
};

const exInput = (txt) => {
    let rsStr = '';
    Object.entries(txt).forEach((key, value) => {
        let tag =
            'input class="input_answers" id="input_answer' + inputIdx + '" ';
        inputIdx += 1;
        let itemStyle = rsStyle(value);
        if (value?.elements?.length > 0) {
            rsStr += '<' + tag + ' style="' + itemStyle + '"' + ' />';
        } else {
            rsStr += '<' + tag + ' style="' + itemStyle + '"' + ' />';
        }
    });
    return rsStr;
};
