const initCoreReading = (rs, v) => {
  getKrtHeaders({
    // version: '1.00.01', examNm: 'KRT_02_17_N_02_C'
    version: studyStore.exprVersion, examNm: model.value + '_C'
  }, (r) => {
    console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    console.log(r.examIds[0]);
    // const tmpHeaderData = (r.header).filter(i => { return i.headerId === rs.content.headerId }).pop();
    const tmpHeaderData = (r.examMaps[r.examIds[0]]).filter(i => { return i.problemId === rs.content.problemId }).pop();

    console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    console.log(tmpHeaderData);
    console.log(rs);
    isLoading.value = false;
    isKreadVisible.value = true;

    // store.openModal(true);
    const tmpEtcInfo = getEtcInfo(rs);
    data.value = {
      content: getHeaderText(rs),
      kread: tmpEtcInfo.kread,
      title: tmpEtcInfo.title,
      etcInfo: tmpEtcInfo,
      standardReadingTime: moment('2022-10-01 00:00:00').add(tmpEtcInfo.solveTime, 's').format('m분ss초'),
      solveTime: tmpEtcInfo.solveTime,
      item: tmpHeaderData ? tmpHeaderData.property : null
    };
    questionHeader.value = getHeader(rs);
    bookNm.value = tmpEtcInfo.bookNm;

  });
}

const setProblem = (rs) => {
  problem.value = {
    problemId: rs.content.problemId,
    data: rs,
    studyTime: 0,
    answer: {},
    reAnswer: null,
    result: null,
    startDtm: moment().format('YYYY-MM-DD HH:mm:ss'),
    endDtm: null,
    questionClass: 'BASE'
  }
}

const nextStep = (v = true) => {
  soundEffect('next');
  if (!problems[problemOrder.value]['startDtm']) {
    problems[problemOrder.value]['startDtm'] = moment().format('YYYY-MM-DD HH:mm:ss');
  }
  problems[(problemOrder.value)]['studyTime'] = problems[(problemOrder.value)]['studyTime'] + moment.duration(moment().diff(startTime)).asSeconds();
  startTime = moment();
  if (notiType === 'last' && v) {
    // 학습 완료
    // 학습 데이터 저장
    return saveResult();
    // return router.push('/authenticreading');
  }
  if (!v) {
    problemOrder.value = problemOrder.value - 1;
  } else {
    problemOrder.value = problemOrder.value + 1;
  }
  if (problemOrder.value === 1) {
    notiType = 'first'
    store.openModal(false);
    console.log(problems);
    questionHeader.value = getHeader(problems[(problemOrder.value)].data);
  } else if (problemOrder.value === Object.keys(problems).length - 1) {
    notiType = 'last'
  } else {
    notiType = 'middle'
  }
  // problemOrder.value = problemOrder.value = 2;
  // getKrtProblem({
  //   version: '1.00.01', problemId: problemMaps[problemOrder.value].problemId
  // }, setProblem)
  problem.value = {
    ...problems[problemOrder.value]
  };
  const inputTypes = ['60', '61', '70'];
  if (inputTypes.includes(problems[problemOrder.value].data.content.property.problem.typeCd)) {
    inputIdx.value = problems[problemOrder.value].data.content.solution.answer.texts.length;
  }
  notiNav !== null && notiNav();
  notiNavBack !== null && notiNavBack();
  notiNav = null;
  notiNavBack = null;
  setNotiNav();
  // startTime = moment();
  console.log('************************************************************');
  console.log(problems);
  console.log('************************************************************');
}

