  }, (rs) => {
    // TODO: store 에 코어리딩 문제 저장
    // rs.examMaps 
    if (rs.examMaps) {
      const exId = rs.examIds[0];
      const problemArr = rs.examMaps[exId];
      // getKrtProblem({
      //   version: '1.00.01', problemId: rs.examMaps[exId][0].problemId
      //   // version: '1.00.01', problemId: 382222
      // }, initCoreReading);
      problemArr.forEach((item, index) => {
        getKrtProblem({
          version: '1.00.01', problemId: item.problemId
          // version: '1.00.01', problemId: 382222
        }, (r) => {
