              reAnswer: null,
              reAnswerIsRight: null,
              result: null,  //'right': 정답, 'halfWrong':1차오답 , 'halfRight':2차정답 , 'wrong':2차오답
              startDtm: null,
              endDtm: null,
              questionClass: 'BASE',
              rightAnswer: r.content.solution.answer.detail,
            }
          }
          if (index === 0) {
            initCoreReading(r, v);
            setProblem(r);
          }
        });
      });
    }
  });
}

