const startTimer = () => {
  isKreadVisible.value = false;
  startTime = moment();
  baseTime = moment().add(data.value.time, "s")
  store.openModal(false);
  cornerTime.textReadStartDtm = moment().format('YYYY-MM-DD HH:mm:ss');
