}

const stopTimer = () => {
  store.openModal(true);
  time.value = moment(moment() - startTime).format('mm분 ss초');
  // diffTime.value = Math.round(moment.duration(baseTime.diff(moment())).asSeconds());
  diffTime.value = diffTime.value + (data.value.solveTime - moment.duration(moment().diff(startTime)).asSeconds());
  problems[problemOrder.value]['studyTime'] = diffTime.value;
  problems[problemOrder.value]['endDtm'] = moment().format('YYYY-MM-DD HH:mm:ss');
  notif = null
  cornerTime.textReadEndDtm = moment().format('YYYY-MM-DD HH:mm:ss');
  cornerTime.textReadTime = diffTime.value;
}

const setSolveTimeMessage = () => {
  let rsMsg = '';
  const tmpTime = Math.round(diffTime.value);
  if (data.value.solveTime < 60) {
    if (tmpTime > 0) {
      rsMsg = (tmpTime + '초 빨리');
    } else {
      rsMsg = ((tmpTime * -1) + '초 늦게');
    }
  } else {
    if (tmpTime > 30) {
      rsMsg = (tmpTime + '초 빨리');
    } else if (tmpTime > 0) {
      rsMsg = (tmpTime + '초 빨리');
    } else {
      rsMsg = ((tmpTime * -1) + '초 늦게');
    }
  }
  return rsMsg;
};

