export const postLessonEnd = async (params, callback) => {
    // const { data } = await service.get(`/api/lesson/start`);
    const { data } = await service({
        url: '/api/lesson/end',
        method: 'post',
        data: params,
    });
    if (data && (data?.statusCode === 200 || data?.statusCode === '200'))
        return callback(data.data);
    return null;
};

export const postAnswer = async (params, callback) => {
    // const { data } = await service.get(`/api/lesson/start`);
    const { data } = await service({
        url: '/api/lesson/answer/' + params.type,
        method: 'post',
        data: params.data,
    });
    if (data && (data?.statusCode === 200 || data?.statusCode === '200'))
        return callback(data.data);
    return null;
};
export const getLessonNext = async (params, callback) => {
    // const { data } = await service.get(`/api/lesson/start`);
    const { data } = await service({
        url: '/api/lesson/next',
        method: 'get',
        params,
    });
    if (data && (data?.statusCode === 200 || data?.statusCode === '200'))
        return callback(data.data);
    return null;
};
/**
 * 보고서 데이터
 * @param {level:1,lesson:1,areaCode:02,learningTypeCode:1} params
 * @param {*} callback
 * @returns
 */
export const getReportArea = async (params, callback) => {
    // const { data } = await service.get(`/api/lesson/start`);
    const { data } = await service({
        url: '/api/report/area',
        method: 'get',
        params,
    });
    if (data && (data?.statusCode === 200 || data?.statusCode === '200'))
        return callback(data.data);
    return null;
};

