import { GlobalStore } from '@/store';

export const soundComplete = () => {
    var soundItem = new Audio('/assets/sound/complete.mp3');
    soundItem.currentTime = 0;
    soundItem.play();
};
export const soundCorrect = () => {
    var soundItem = new Audio('/assets/sound/correct.mp3');
    soundItem.currentTime = 0;
    soundItem.play();
};
