/**
 * 효과음 재생
 * @param {*} val // 효과음
 */
export const soundEffect = (val) => {
    const globalStore = GlobalStore();
    if (!globalStore?.sound) {
        return false;
    }
    let soundItem = null;
    if (val === 'complete') {
        soundItem = new Audio('/assets/sound/complete.mp3');
    } else if (val === 'correct') {
        soundItem = new Audio('/assets/sound/correct.mp3');
    } else if (val === 'enter') {
        soundItem = new Audio('/assets/sound/enter.mp3');
    } else if (val === 'exit') {
        soundItem = new Audio('/assets/sound/exit.mp3');
    } else if (val === 'next') {
        soundItem = new Audio('/assets/sound/next.mp3');
    } else if (val === 'popup') {
        soundItem = new Audio('/assets/sound/popup.mp3');
    } else if (val === 'result') {
        soundItem = new Audio('/assets/sound/result.mp3');
    } else if (val === 'tic') {
        soundItem = new Audio('/assets/sound/tic.mp3');
    }
    if (soundItem) {
        soundItem.currentTime = 0;
        soundItem.play();
    }
};
