<template>

    <!-- <strong v-if="important == true" class="template__important">중요해요</strong> -->
    <!-- <span class="marking" :class="[markingClass, important == true ? 'withImportant' : '']"></span> -->
    <h6 class="template__title" v-if="!isCsat">
        <div style="display: flex;align-items: end;">
            <!-- <div :class="markingClass"> </div> -->
            <AnswerMarking :marking-class="markingClass" />
            <div class="template__no"> {{ props.pContent?.problemNo }}. </div>
            <q-chip size="sm" style="background-color: #EDEDED;" text-color="black">
                {{ props.pContent.etcInfo?.analysis }}
            </q-chip>
            <!-- <q-chip>기본 문제 <span style="color: red;">O</span></q-chip> -->
            <q-chip v-if="drillChip.length > 0" size="sm" style="background-color: #D8F6FF;" v-for="item in drillChip">
                {{ item.label }} <span style="color:red">{{ item.drill === 'up' ? 'O' : 'X' }}</span>
            </q-chip>
            <q-chip size="sm" style="background-color: #D8F6FF;">{{ props.pContent.etcInfo?.difficultNm }}</q-chip>
        </div>
    </h6>
    <div style="text-align: right;" v-if="isCsat">
        <q-radio v-if="choiceArrange" v-for="(item, index) in choiceArrange.data"
            :class="'col-' + (12 / (choiceArrange.array === '0' ? 1 : Number(choiceArrange.array)))"
            style="padding: 10px;" v-model="choice" :val="item.numChar"
            v-bind:class="rightAnswer !== null && rightAnswer === item.numChar ? 'item-correct' : item.numChar === choice ? 'item-select' : 'item-nomal'"
            @update:model-value="onChangeAnswer"
            :disable="props.pContent.result && props.pContent.result.marking !== 'halfWrong'">
            <div style="display: inline-flex; width: 100%;">
                <div style="display: flex; justify-content: center;align-items: center; margin-right: 10px;">
                    <div class="item-num">
                        {{ item.num }}
                    </div>
                </div>
            </div>
        </q-radio>
    </div>
    <div>
        <!-- <q-input clearable outlined v-model="answer.value1" /> -->
        <!-- <span v-html="question"></span> -->
        <AnswerMarking :marking-class="markingClass" v-if="isCsat" style="margin-top: 38px;position: absolute" />
        <!-- <AnswerMarking :marking-class="markingClass" v-if="isCsat" /> -->
        <v-runtime-template :template="question" style="margin-left: 13px;"></v-runtime-template>
        <div class="row" style="width:100%;">
            <q-radio v-if="choiceArrange" v-for="(item, index) in choiceArrange.data"
                :class="'col-' + (12 / (choiceArrange.array === '0' ? 1 : Number(choiceArrange.array)))"
                style="padding: 10px;" v-model="choice" :val="item.numChar"
                v-bind:class="rightAnswer !== null && rightAnswer === item.numChar ? 'item-correct' : item.numChar === choice ? 'item-select' : 'item-nomal'"
                @update:model-value="onChangeAnswer"
                :disable="props.pContent.result && props.pContent.result.marking !== 'halfWrong'">
                <div style="display: inline-flex; width: 100%;">
                    <div style="display: flex; justify-content: center;align-items: center; margin-right: 10px;">
                        <div class="item-num">
                            {{ item.num }}
                        </div>
                    </div>
                    <div v-html="item.text"></div>
                </div>
            </q-radio>
        </div>
        <!-- <q-option-group v-model="choice" :options="options" color="red" /> -->
    </div>
    <div>

    </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref, defineEmits, defineProps, watch, toRaw } from 'vue';
import { getQuestion_old, getChoice } from "@/helpers/qbankParser/qbankDataParse";
import { useQuasar } from 'quasar';
import VRuntimeTemplate from "vue3-runtime-template";
import AnswerMarking from './answerMarking.vue';
import { difficult, drill } from '@/helpers/qbankParser/qbankMeta';

const question = ref('');
const choiceArrange = ref([]);
const choice = ref('');
const rightAnswer = ref(null);
const isCsat = ref(false);
let markingClass = '';
let drillChip = ref([]);

const emit = defineEmits(['setAnswer']);
const $q = useQuasar();

const props = defineProps({
    'pContent': {
        default: {}
    },
    'isCsat': false
});


const onChangeAnswer = (v, evt) => {
    emit('setAnswer', v);
}


watch(() => props.pContent, (newValue) => {
    // const questionHeader = JSON.parse(JSON.stringify(newValue));
    const vContent = toRaw(newValue);
    // parsingContent(vContent.data.content);
    console.log(vContent.data);
    question.value = getQuestion_old(vContent.data, isCsat.value ? vContent.problemNo : false);
    choiceArrange.value = getChoice(vContent.data);
    console.log('vContent.answer $$$$$$$$$$$$$$$$$$$$$$$$$$$$$');
    console.log(vContent.answer);
    rightAnswer.value = null;
    if (vContent.reAnswer) {
        choice.value = vContent.reAnswer;
    } else if (vContent.answer) {
        choice.value = vContent.answer;
    } else {
        choice.value = '';
    }
    if (vContent.result) {
        markingClass = 'marking-' + vContent.result.marking;
        if (vContent.result.marking !== 'halfWrong') {
            setTimeout(() => {
                const item = document.querySelectorAll('.input_answers');
                item.forEach((target) => {
                    target.setAttribute('readonly', true);
                });
            }, 200);
            rightAnswer.value = vContent.data.content.solution.answer?.detail[0]?.answer;
            console.log(rightAnswer.value)
        }
    } else {
        markingClass = '';
    }

    drillChipsInit(vContent);
});

onMounted(() => {
    const vContent = toRaw(props.pContent);
    isCsat.value = props.isCsat;
    console.log(vContent);
    question.value = getQuestion_old(vContent.data, isCsat.value ? vContent.problemNo : false);
    choiceArrange.value = getChoice(vContent.data);
    if (vContent.answer && vContent.answer.length > 0) {
        vContent.answer.map(i => {
            setTimeout(() => {
                document.getElementById('choiceList_' + i).checked = true;
            }, 100);
        })
    }
    drillChipsInit(vContent);
})

const drillChipsInit = (vContent) => {
    drillChip.value = [];
    if (vContent.questionClass === 'DRILL_DOWN') {
        if (vContent.baseDifficult === '03') {
            drillChip.value.push({ label: difficult['03'].label, drill: 'down' });
        } else {
            for (var a = Number(vContent.baseDifficult); a > Number(vContent.etcInfo.difficultCd); a--) {
                drillChip.value.push({ label: difficult['0' + a].label, drill: 'down' });
            }
        }
    }
    if (vContent.questionClass === 'DRILL_UP') {
        for (var a = Number(vContent.baseDifficult); a < Number(vContent.etcInfo.difficultCd); a++) {
            drillChip.value.push({ label: difficult['0' + a].label, drill: 'up' });
        }
    }
}

onUnmounted(() => {
    // 
    drillChip.value = [];
})

</script>

<style lang="scss" scoped>
