    &__no {
        margin: 0px 13px;
    }
}

::v-deep input {
    // width: 500px;
    // height: 32px;
    // font-size: 15px;

    border: 1px solid rgb(233, 233, 233);
    border-radius: 5px;
    outline: none;
    margin: 0px 5px;
    text-align: center;
    max-width: 100%;
}

::v-deep textarea {
    // width: 500px;
    // height: 32px;
    // font-size: 15px;

    border: 1px solid rgb(233, 233, 233);
    border-radius: 5px;
    outline: none;
    margin: 0px 5px;
    max-width: 100%;
}

::v-deep p {
    margin: 0px;
}


.radio {
    &__wrap {
        grid-template-columns: repeat(2, size(120));
        grid-template-rows: repeat(2, size(120));
    }
}

