                        top: 0;
                        box-sizing: border-box;
                        border-radius: 9px;
                    }

                    &.correct {
                        border-color: #00a7ff;
                        color: #00a7ff;

                        &:before,
                        &:after {
                            border-color: #00a7ff;
                            color: #00a7ff;
                        }

                        ::v-deep u {
                            color: #00a7ff;
                        }
                    }

                    &.wrong {
                        border-color: #ff0000;
                        color: #ff0000;

                        &:before,
                        &:after {
                            border-color: #ff0000;
                            color: #ff0000;
                        }

                        ::v-deep u {
                            color: #ff0000;
                        }
                    }
                }
            }
        }
    }
}

.template {
