        }
    }


    &-correct {
        // background-color: #d7eaff;
        border-radius: 10px;

        .item-num {
            border: 1px solid #118AF1;
            background-color: #118AF1;
            color: white;
            border-radius: 20px;
            padding: 0px 8px 2px;
        }
    }

    &-wrong {
        // background-color: #ffc8d6;
        border-radius: 10px;

        .item-correct_num {
