const store = DialogStore();
const globalStore = GlobalStore();
const studyStore = StudyStore();
const router = useRouter();
const value = 0.2;
const alert = ref(null);
const progress = 0.3;
const { getTodayLesson, getUserType } = HOME_API();

const todayLesson = ref({});
const userType = ref({});

const areaCodeArr = ref({ '01': [], '02': [] });
const $q = useQuasar();


alert.value = false;

const openAlert = () => {
  alert.value = true;
}

const openDialog = () => {
  store.openModal(true);
};

const logOut = () => {
  globalStore.setToken(undefined);
  globalStore.setReToken(undefined);
  studyStore.initStore();
  router.push("/login")
}

const moveToPage = (path) => {
  // TODO: 테스트후 변경
  // router.push({ path })
  $q.dialog({
    title: '확인',
    message: '서비스 준비중입니다.',
    cancel: true,
    persistent: true
  })
}

onMounted(() => {
  // const getTodayData = (result) => {
  //   todayLesson.value = result
  // }

  // const getUserInfo = (result) => {
  //   userType.value = result
  // }

  // getTodayLesson(null, getTodayData)
  // getUserType(null, getUserInfo)

  getUserJson((rs) => {
    console.log('@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@');
    console.log(rs);
    studyStore.initStore();
    const userJsonData = rs !== '' ? JSON.parse(rs) : null;
    console.log(userJsonData);
    getUserMain((rs) => {
      console.log(rs)
      let tmpMapTree = [];
      let tmpStudy = { '01': [], '02': [] };
      rs.studyMain.forEach(element => {
        tmpStudy[element.areaCode] = [...tmpStudy[element.areaCode], element];
        tmpMapTree.push({
          examCode: element.examName.slice(0, element.examName.length - 2),
          version: rs.exprVersion,
          // TODO: 변경해야함
          headerEtcCd02: element.areaCode === '01' ? '05' : '02',
          headerEtcCd01: element.passageClass,
          title: element.passageName,
          areaCode: element.areaCode,
          isDone: false,
          learningStatus: element.learningStatus,
          learningType: element.learningType,
          difficult: element.areaCode === '01' ? JSON.parse(rs.difficultyL) : JSON.parse(rs.difficultyN)
        });
      });
      areaCodeArr.value = tmpStudy;
      studyStore.level = rs.level;
      studyStore.lesson = rs.lesson;
      studyStore.exprVersion = rs.exprVersion;
      console.log(tmpMapTree);
      studyStore.mapTree.exams = tmpMapTree;
      studyStore.difficultyL = JSON.parse(rs.difficultyL);
      studyStore.difficultyN = JSON.parse(rs.difficultyN);
      // TODO: 변경 필요함
      const fIndex = _.findIndex(rs.studyMain, function (o) { return o.examName === rs.currentStart.examName; });
      studyStore.process.examIdx = fIndex;
      if (rs.currentStart.learningStatus === 'W') {
        studyStore.process.studyTree = 5082;
        studyStore.process.learningHistorySeq = null;
      } else {
        if (!userJsonData) { studyStore.process.studyTree = 5082 }
        if (userJsonData && userJsonData?.studyTree) {
          studyStore.process.studyTree = Number(userJsonData?.studyTree);
        }
        if (userJsonData && userJsonData?.learningHistorySeq) {
          studyStore.process.learningHistorySeq = Number(userJsonData?.learningHistorySeq);
        }
      }
      studyStore.process.problemOrd = 0;
      studyStore.process.exam = {
        examCode: rs.currentStart.examName.slice(0, rs.currentStart.examName.length - 2),
        version: rs.exprVersion,
        // TODO: 변경해야함
        headerEtcCd02: rs.currentStart.areaCode === '01' ? '05' : '02',
        headerEtcCd01: rs.currentStart.passageClass,
        title: rs.currentStart.passageName,
        areaCode: rs.currentStart.areaCode,
        isDone: false,
        learningType: rs.currentStart.learningType,
        learningStatus: rs.currentStart.learningStatus,
        // difficult: rs.currentStart.areaCode === '01' ? JSON.parse(rs.difficultyL) : JSON.parse(rs.difficultyN)
      };

      console.log(studyStore);
    });

  })
  // getUserInfoApi((rs) => {
  //   console.log('@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@');
  //   console.log(rs);

  // })


})

const studyStart = () => {
  router.push(studyStore.process.studyTree === 5083 ? '/authenticreading' : studyStore.process.studyTree === 5084 ? '/vocabularylearning' : 'corereading');
}

const test = () => { }
</script>

