<template>
    <div class="row fullscreen">
        <div class="col">
            <div class="text-h8 q-pa-sm text-bold">대교 KRead독해 진단평가</div>
            <div class="q-pa-xl text-center">
                <div class="text-h6 text-bold text-center">
                    상위 1% 학습법으로 독해학습을 시작해 보세요.
                </div>
                <br />
                <div class="text-center">
                    <p>
                        KRead독해 진단평가 프로그램에서는 <br />자신의
                        KReaD지수에 맞게 잘 다듬어진 다양한 분야의 글을 마음껏
                        읽고<br />
                        양질의 기출 문제를 풀어볼 수 있습니다.<br />
                        KRead독해 진단평가를 통해 학습지의 KReaD지수와 독해에서
                        부족한 영역을 확인해 보세요.
                        <br /><br />
                        알맞은 난이도의 영어 책을 선택하는데 렉사일 지수를
                        참고할 수 있습니다.<br />
                        렉사일은 자신의 현재 독해력 수준을 측정하여 지적
                        호기심을 갖고,<br />
                        지적 성취를 얻을만한 독서를 할 수 있도록 책을 선택할 때
                        도움을 주기 위한 지수입니다.<br /><br />
                        영어실력 향상을 위하여 영어 원서를 읽는다면<br />
                        자신의 렉사일 지수와 일치하거나 다소 높은 책을
                        선택하고,<br />
                        평소 흥미가 없거나 어려운 분야의 책을 읽어야 한다면<br />
                        렉사일 지수가 다소 낮은 책을 선택하는 것이 효과적일 수
                        있습니다.<br />
                        미국을 비롯한 전 세계 응시자 표본의 객관적인 영어 실력을
                        평가받을 수 있으며,<br />
                        정기적인 응시를 통해 읽기 능력의 향상 여부를 측정할 수
                        있습니다.
                    </p>
                </div>
            </div>
        </div>
        <div class="col bg-blue-grey-1 text-center flex center">
            <div class="q-mx-xl" style="flex: 1">
                <q-form
                    @submit="onSubmit"
                    ref="loginFormRef"
                    class="q-gutter-md"
                >
                    <p class="header-label">이름</p>

                    <q-input
                        bg-color="white"
                        outlined
                        v-model="name"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '이름을 입력해 주세요.',
                            validMaxLength,
                            validDenyEnglish,
                            validDenySign,
                            validDenyNumber,
                        ]"
                    />

                    <p class="header-label">이메일</p>
                    <q-input
                        bg-color="white"
                        outlined
                        v-model="email"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '이메일을 입력해 주세요.',
                            validIncludeAt,
                            validMaxLength50,
                        ]"
                    />

                    <p class="header-label">학교</p>

                    <q-card flat bordered class="my-card" @click="onTest">
                        <q-card-section>
                            <div class="text-h6">
                                {{ selectedSchoolName }}
                            </div>
                        </q-card-section>
                    </q-card>

                    <p class="header-label">학년</p>
                    <q-select
                        filled
                        name="grade"
                        v-model="grade"
                        :options="optionsGrade"
                        option-value="value"
                        option-label="label"
                        option-disable="inactive"
                        :rules="[
                            (val) =>
                                (val && val.label !== '') ||
                                '학년을 선택해 주세요.',
                        ]"
                    />

                    <p class="header-label">전화번호</p>
                    <q-input
                        bg-color="white"
                        outlined
                        v-model="phoneNumber"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '전화번호를 입력해 주세요.',
                            validMaxLength,
                            validPhoneNo,
                        ]"
                    />
                </q-form>
                <div class="login-btn q-mt-xl">
                    <q-btn
                        class="text-h6"
                        unelevated
                        rounded
                        color="primary"
                        @click="onSubmit"
                        type="submit"
                        label="진단평가 시작하기"
                    />
                </div>
            </div>
        </div>
    </div>
    <q-dialog v-model="onPopSchool">
        <q-card style="width: 600px">
            <q-card-section>
                <div class="text-h6">지역 및 학교명</div>
            </q-card-section>

            <q-separator />
            <q-select
                class="col q-mt-lg q-mx-lg"
                outlined
                v-model="schoolRegion"
                :options="optionsRegion"
                label="지역 선택"
                :rules="[
                    (val) =>
                        (val && val.value !== '') || '지역을 선택해 주세요.',
                ]"
            />
            <div class="row">
                <q-input
                    class="col q-mx-lg"
                    v-model="keywordSchool"
                    debounce="1000"
                    filled
                    placeholder="학교명 검색"
                    hint="학교명을 기재하면 자동검색됩니다."
                    :rules="[
                        (val) =>
                            (val && val.length > 0 && val.length < 15) ||
                            '15자 이내로 입력해 주세요.',
                        validDenyEnglish,
                        validDenyNumber,
                        validDenySign,
                    ]"
                >
                    <template v-slot:append>
                        <q-icon name="search" />
                    </template>
                </q-input>
            </div>

            <q-card-section style="max-height: 50vh" class="scroll">
                <q-list bordered>
                    <q-item
                        v-for="sch in arrResSchool"
                        :key="sch.code"
                        class="q-my-sm"
                        @click="onItemSchool(sch)"
                        clickable
                        v-ripple
                    >
                        <!-- <q-item-section avatar>
                          <q-avatar color="primary" text-color="white">
                              {{ sch.letter }}
                          </q-avatar>
                      </q-item-section> -->

                        <q-item-section>
                            <q-item-label>{{ sch.name }}</q-item-label>
                            <q-item-label caption lines="1">{{
                                sch.name
                            }}</q-item-label>
                        </q-item-section>

                        <!-- <q-item-section side>
                          <q-icon name="chat_bubble" color="green" />
                      </q-item-section> -->
                    </q-item>

                    <q-separator />
                </q-list>
            </q-card-section>
        </q-card>
    </q-dialog>
    <q-dialog
        v-model="onPopSchoolSecond"
        transition-show="scale"
        transition-hide="scale"
    >
        <q-card class="bg-teal text-white" style="width: 300px">
            <q-card-section>
                <div class="text-h6">{{ selectedSchoolName }}</div>
            </q-card-section>

            <q-card-section class="q-pt-none">
                위 학교로 선택할까요?
            </q-card-section>

            <q-card-actions align="around" class="bg-white text-teal">
                <q-btn
                    flat
                    label="네"
                    @click="
                        () => {
                            onPopSchool = false;
                            onPopSchoolSecond = false;
                        }
                    "
                />
                <q-btn flat label="다시 선택" v-close-popup />
            </q-card-actions>
        </q-card>
    </q-dialog>
</template>

<script setup>
import { useQuasar } from 'quasar';
import { ref, reactive, watch } from 'vue';
import { GlobalStore } from '@/store';
import { useRouter } from 'vue-router';
import { loginApi } from '@/api/modules/login';
// import optionsRegion from '@/views/login/optionRegion.json';

const onTest = () => {
    console.log('--onTest--');
    onPopSchool.value = true;
};
const onTest123 = () => {
    console.log('--onTest123--');
    onPopSchoolSecond.value = true;
};
const onClose = () => {
    console.log('--onClose--');
    onPopSchool.value = false;
};

const $q = useQuasar();
const router = useRouter();

const dense = ref(false);
const name = ref('');
const email = ref('');
const grade = ref(); // 등급
const phoneNumber = ref('');
const loginFormRef = ref(null);

const optionsGrade = [
    {
        value: 1,
        label: '예비초-중1',
    },
    {
        value: 2,
        label: '중2',
    },
    {
        value: 3,
        label: '중3',
    },
];

///////////////////////----학교검색----///////////////////////
const onPopSchool = ref(false);
const onPopSchoolSecond = ref(false);
const schoolRegion = ref(null);
const keywordSchool = ref();
const selectedSchoolName = ref('클릭하여 학교 검색하기');
const selectedSchoolAddress = ref();

watch(onPopSchool, (newonPopSchool, oldonPopSchool) => {
    console.log('--onPopSchool:', newonPopSchool, oldonPopSchool);
    if (newonPopSchool) {
        onPopSchool.value = true;
        // schoolRegion.value = null;
        // keywordSchool.value = '';
        // selectedSchoolName.value = '';
    }
});
watch(keywordSchool, (newSch, oldSch) => {
    console.log('--keywordSchool:', newSch, oldSch);
    console.log('--keywordSchool-validate-:', keywordSchool.value.validate());
});

const onItemSchool = (sch) => {
    console.log('--onSchool--', sch.name);
    selectedSchoolName.value = sch.name;
    selectedSchoolAddress.value = sch.address;
    onPopSchoolSecond.value = true;
};
const funOpenPopResultSchool = () => {
    console.log('--funOpenPopResultSchool--');
};
const arrResSchool = reactive([
    {
        name: '가가가학교1',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교2',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교3',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교4',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교5',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교6',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교7',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교8',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교9',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교10',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교11',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교12',
        code: 423,
        address: '주소입니다.',
    },
    {
        name: '가가가학교13',
        code: 423,
        address: '주소입니다.',
    },
]);
const optionsRegion = [
    {
        value: 'C10',
        label: '서울특별시교육청',
    },
    {
        value: 'B10',
        label: '부산광역시교육청',
    },
    {
        value: 'D10',
        label: '대구광역시교육청',
    },
    {
        value: 'E10',
        label: '인천광역시교육청',
    },
    {
        value: 'F10',
        label: '광주광역시교육청',
    },
    {
        value: 'G10',
        label: '대전광역시교육청',
    },
    {
        value: 'H10',
        label: '울산광역시교육청',
    },
    {
        value: 'I10',
        label: '세종특별자치시교육청',
    },
    {
        value: 'J10',
        label: '경기도교육청',
    },
    {
        value: 'K10',
        label: '강원도교육청',
    },
    {
        value: 'M10',
        label: '충청북도교육청',
    },
    {
        value: 'N10',
        label: '충청남도교육청',
    },
    {
        value: 'P10',
        label: '전라북도교육청',
    },
    {
        value: 'Q10',
        label: '전라남도교육청',
    },
    {
        value: 'R10',
        label: '경상북도교육청',
    },
    {
        value: 'S10',
        label: '경상남도교육청',
    },
    {
        value: 'T10',
        label: '제주특별자치도교육청',
    },
];

///////////////////////----유효성검사----///////////////////////
const pattern_kor = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/; // korean
const pattern_eng = /[a-zA-Z]/; // alphabet
const pattern_spc = /[~!@#$%^&*()_+|<>?:{}]/; // sign
const pattern_num = /[0-9]/; // number

const validMaxLength = (val) =>
    val.length > 13 ? '13자 이내로 적어주세요.' : true;

const validMaxLength50 = (val) =>
    val.length > 50 ? '글자수가 너무 많습니다.' : true;

const validDenyKorean = (val) =>
    pattern_kor.test(val) ? '한글은 적을 수 없습니다.' : true;
const validDenyEnglish = (val) =>
    pattern_eng.test(val) ? '알파벳은 적을 수 없습니다.' : true;
const validDenySign = (val) =>
    pattern_spc.test(val) ? '특수문자는 적을 수 없습니다.' : true;
const validDenyNumber = (val) =>
    pattern_num.test(val) ? '숫자는 적을 수 없습니다.' : true;

const validIncludeAt = (val) =>
    !(val.includes('@') && val.includes('.'))
        ? '유효한 이메일을 적어주세요.'
        : true;

const validPhoneNo = (val) =>
    pattern_eng.test(val) || pattern_spc.test(val) || pattern_kor.test(val)
        ? '숫자와 하이픈(-)만 적을 수 있습니다.'
        : val.length < 9
        ? '유효한 전화번호를 적어주세요.'
        : true;

///////////////////////----Onclick----///////////////////////

const onSubmit = () => {
    // console.log(grade);
    console.log('--loginFormRef-validate-:', loginFormRef.value.validate());

    loginFormRef.value.validate().then((success) => {
        if (success) {
            const saveCallback = async (data) => {
                if (data) {
                    const globalStore = GlobalStore();

                    globalStore.setUserInfo({
                        name: name.value,
                        email: email.value,
                        grade: grade.value.value,
                        phoneNumber: phoneNumber.value,
                    });
                    router.push('/');
                }
            };
            loginApi(
                {
                    stdntNm: name.value,
                    email: email.value,
                    phoneNo: phoneNumber.value,
                },
                saveCallback,
            );
        } else {
            $q.notify({
                color: 'red-4',
                textColor: 'white',
                icon: 'cloud_done',
                message: '모든 항목을 입력해 주세요.',
            });
        }
    });
};
</script>

<style scoped lang="scss">
.center {
    justify-content: center;
    align-items: center;
}

.header-label {
    margin-bottom: 8px;
    text-align: left;
    font-weight: 500;
}
</style>
