    problems[problemOrder.value]['solvingEndTs'] = moment().format('YYYY-MM-DD HH:mm:ss');
  }
  startTime = moment();
  if (no == -1 && problems[(problemOrder.value)] && problems[(problemOrder.value)]['result'] !== null && problems[(problemOrder.value)]['result']['marking'] !== 'halfWrong') {
    problems[problemOrder.value]['done'] = true;
  }

  if (notiType === 'last' && v) {
