  nextStep(false, 1);
  // problemOrder.value = 1;
}

const saveResult = () => {
  let tmpRs = [];
  const addRs = (arr) => {
    Object.entries(arr).forEach(([key, value]) => {
      let tmpVal = { ...value };
      // tmpVal.rightAnswer = tmpVal.data.content.solution.answer.detail[0].answer;
      tmpVal.correct = tmpVal.data.content.solution.answer.detail[0].answer;
      tmpVal.answer = tmpVal.answer;
      tmpVal.correctFlag = tmpVal.result?.marking === 'right' ? 'Y' : 'N';
      tmpVal.reAnswer = tmpVal.reAnswer;
      tmpVal.reCorrectFlag = tmpVal.reAnswer === null ? null : tmpVal.result?.marking === 'halfRight' ? 'Y' : 'N';
      tmpVal.elapsedTime = tmpVal.elapsedTime * 1000;

      delete tmpVal.problemNo;
      delete tmpVal.analysisOrd;
      delete tmpVal.etcInfo;
      delete tmpVal.data;
      delete tmpVal.type;
      delete tmpVal.markingType;

      // TODO: 비디오 시간 필요 여부
      delete tmpVal.videoTime;
      delete tmpVal.relationResult;
      delete tmpVal.result;
      delete tmpVal.done;
      delete tmpVal.scrollCheck;
      delete tmpVal.relationId;
      delete tmpVal.completeVideo;
      delete tmpVal.videoUrl;

      delete tmpVal.baseDifficult;
      delete tmpVal.prevLabel;
      tmpRs.push(tmpVal);
    })
  }
  addRs(baseProblems);
  addRs(problems);
  const saveData = {
    examName: studyStore.process.exam.examCode + '_R',
    // examNm: 'KRT_01_02_L_01',
    exprVersion: studyStore.exprVersion,
    // learningType: studyStore.process.exam.learningType,
    learningType: "NORMAL",
    learningHistorySeq: studyStore.process.learningHistorySeq,
    cornerTime: { ...cornerTime, cornerLearningEndTs: moment().format('YYYY-MM-DD HH:mm:ss'), cornerLearningTime: (moment.duration(moment().diff(cornerLearningBaseTime)).asSeconds()) * 1000 },
    submission: tmpRs
  }
  console.log('saveData %%%%%%%%%%%%%%%%%%%%%%%%%');
  console.log(saveData);
  postAnswer({ type: 'earnest ', data: saveData }, () => {
    router.push('/vocabularylearning');
    studyStore.process.studyTree = studyStore.process.studyTree + 1;
    postUserJson(JSON.stringify(studyStore.process), (rs) => {
      //
    });
  })
}

