<template>
    <div ref="chartdiv" style="width:100%; height: 100%;"></div>
</template>

<script setup>
import { onMounted, onUnmounted, ref } from 'vue';
import * as am5 from '@amcharts/amcharts5';
import * as am5xy from '@amcharts/amcharts5/xy';
import * as am5radar from '@amcharts/amcharts5/radar';
import am5themes_Animated from '@amcharts/amcharts5/themes/Animated';

const chartdiv = ref(null);
const props = defineProps([
    'chartData'
])
let root;
onMounted(() => {
    root = am5.Root.new(chartdiv.value);

    // Set themes
    // https://www.amcharts.com/docs/v5/concepts/themes/
    root.setThemes([
        am5themes_Animated.new(root)
    ]);

    // Create chart
    // https://www.amcharts.com/docs/v5/charts/radar-chart/
    var chart = root.container.children.push(am5radar.RadarChart.new(root, {
        // panX: false,
        // panY: false,
        // wheelX: "panX",
        // wheelY: "zoomX",
        innerRadius: am5.percent(80),
        startAngle: -180,
        endAngle: 0,
        paddingTop: -50,
        paddingBottom: -50,
        paddingLeft: -30,
        paddingRight: -30,
    }));

    chart.radarContainer.children.push(
        am5.Label.new(root, {
            text:
                "[fontSize:1em]독해력[/]\n[fontSize:2.5em;fontWeight:700]" + (props?.chartData ? props.chartData : 0) + "[fontSize:1.5em;fontWeight:700]%",
            textAlign: "center",
            centerX: am5.percent(50),
            centerY: am5.percent(100),
        })
    );

    // Data
    var data = [{
        category: "Research",
        value: props?.chartData ? props.chartData : 0,
        full: 100,
        columnSettings: {
            fill: chart.get("colors").getIndex(0)
        }
    }];

    // Add cursor
    // https://www.amcharts.com/docs/v5/charts/radar-chart/#Cursor
    // var cursor = chart.set("cursor", am5radar.RadarCursor.new(root, {
    //   behavior: "zoomX"
    // }));

    // cursor.lineY.set("visible", false);

    // Create axes and their renderers
    // https://www.amcharts.com/docs/v5/charts/radar-chart/#Adding_axes
    var xRenderer = am5radar.AxisRendererCircular.new(root, {
        //minGridDistance: 50
        disabled: true
    });

    // xRenderer.labels.template.setAll({
    //   // radius: 10
    // });

    xRenderer.grid.template.setAll({
        forceHidden: true
    });

    var xAxis = chart.xAxes.push(am5xy.ValueAxis.new(root, {
        renderer: xRenderer,
        min: 0,
        max: 100,
        strictMinMax: false,
        numberFormat: "#'%'",
        tooltip: am5.Tooltip.new(root, {}),
        visible: false,
    }));


    var yRenderer = am5radar.AxisRendererRadial.new(root, {
        minGridDistance: 100
    });

    yRenderer.labels.template.setAll({
        // centerX: am5.p100,
        // fontWeight: "500",
        // fontSize: 18,
        templateField: "columnSettings"
    });

    yRenderer.grid.template.setAll({
        forceHidden: true
    });

    var yAxis = chart.yAxes.push(am5xy.CategoryAxis.new(root, {
        categoryField: "category",
        visible: false,
        renderer: yRenderer
    }));

    yAxis.data.setAll(data);

    xAxis.cursorTooltipEnabled = false;
    yAxis.cursorTooltipEnabled = false;

    // Create series
    // https://www.amcharts.com/docs/v5/charts/radar-chart/#Adding_series
    var series1 = chart.series.push(am5radar.RadarColumnSeries.new(root, {
        xAxis: xAxis,
        yAxis: yAxis,
        clustered: false,
        valueXField: "full",
        categoryYField: "category",
        fill: root.interfaceColors.get("alternativeBackground")
    }));

    series1.columns.template.setAll({
        width: am5.p100,
        fillOpacity: 0.08,
        strokeOpacity: 0,
        cornerRadius: 20
    });

    series1.data.setAll(data);

    var series2 = chart.series.push(am5radar.RadarColumnSeries.new(root, {
        xAxis: xAxis,
        yAxis: yAxis,
        clustered: false,
        valueXField: "value",
        categoryYField: "category"
    }));

    series2.columns.template.setAll({
        width: am5.p100,
        strokeOpacity: 0,
        // tooltipText: "{category}: {valueX}%",
        cornerRadius: 20,
        // templateField: "columnSettings"
    });



    series2.data.setAll(data);


    // Animate chart and series in
    // https://www.amcharts.com/docs/v5/concepts/animations/#Initial_animation
    // series1.appear(2000);
    series2.appear(3000);
    // chart.appear(1000, 100);
})

onUnmounted(() => {
    root.dispose();
})
</script>

<style scoped lang="scss">

</style>
