/** 뒤로가기
 *
 */
export const historyBack = () => {
    window.history.back();
};

/**
 * camelcase -> kebabize
 * @param {*} str
 * @returns
 */
export const kebabize = (str) =>
    str.replace(
        /[A-Z]+(?![a-z])|[A-Z]/g,
        ($, ofs) => (ofs ? '-' : '') + $.toLowerCase(),
    );

/**
 * 초 => 00:00:00 으로변경
 * @param {*} seconds
 * @returns string
 */
export const getTimeStringSeconds = (seconds) => {
    var hour, min, sec;
    hour = parseInt(seconds / 3600);
    min = parseInt((seconds % 3600) / 60);
    sec = seconds % 60;
    if (hour.toString().length == 1) hour = '0' + hour;
    if (min.toString().length == 1) min = '0' + min;
    if (sec.toString().length == 1) sec = '0' + sec;
    return (hour !== '00' ? hour + ':' : '') + min + ':' + sec;
};

/**
 * 초 => n시 n분 n초 로변경
 * @param {*} seconds
 * @returns string
 */
export const getTimeStringSecondsKor = (seconds) => {
    var hour, min, sec;
    hour = parseInt(seconds / 3600);
    min = parseInt((seconds % 3600) / 60);
    sec = parseInt(seconds % 60);
    // if (hour.toString().length == 1) hour = '0' + hour;
    // if (min.toString().length == 1) min = '0' + min;
    // if (sec.toString().length == 1) sec = '0' + sec;
    return (
        (hour == 0 ? '' : hour + '시간') +
        (min === 0 ? '' : min + '분') +
        (sec === 0 ? '' : sec + '초')
    );
};
/**
 *  n시 n분 n초 => 초 로변경
 * @param {*} seconds
 * @returns string
 */
export const timeStringSecondsKorToSec = (str) => {
    if (!str) return 0;
    let secTime = 0;
    let tmpStr = str;
    if (tmpStr.indexOf('시간') > -1) {
        try {
            let tmp = tmpStr.split('시간');
            secTime = Number(tmp[0]) * 3600;
            tmpStr = tmp[1];
        } catch (error) {
            console.log('error');
        }
    }
    if (tmpStr.indexOf('분') > -1) {
        try {
            let tmp = tmpStr.split('분');
            secTime += Number(tmp[0]) * 60;
            tmpStr = tmp[1];
        } catch (error) {
            console.log('error');
        }
    }
    if (tmpStr.indexOf('초') > -1) {
        try {
            let tmp = tmpStr.split('초');
            secTime += Number(tmp[0]);
            tmpStr = tmp[1];
        } catch (error) {
            console.log('error');
        }
    }
    return secTime;
};

/**
 * 태그제거
 * @param {*} str
 * @returns string
 */
export const stripTags = (str) => {
    if (str) {
        str = str.replace(/(<([^>]+)>)/gi, ''); // 태그 제거
        str = str.replace(/\s\s+/g, ' '); // 연달아 있는 줄바꿈, 공백, 탭을 공백 1개로 줄임
        str = str.replaceAll('&nbsp', '');
    }
    return str;
};

export const textAreaResize = () => {
    const txHeight = 26;
    const tx = document.getElementsByTagName('textarea');

    for (let i = 0; i < tx.length; i++) {
        if (tx[i].value == '') {
            tx[i].style.height = 'auto';
            tx[i].style.height = txHeight + 'px';
            tx[i].style.overflowY = 'hidden';
        } else {
            tx[i].style.height = 'auto';
            tx[i].style.height = tx[i].scrollHeight + 'px';
            tx[i].style.overflowY = 'hidden';
        }
        tx[i].removeEventListener('input', OnInput, false);
        tx[i].addEventListener('input', OnInput, false);
    }

    function OnInput(e) {
        this.style.height = 0;
        this.style.height = this.scrollHeight + 'px';
    }
};

export const checkPassword = (txt) => {
    if (/^(?=.*[a-zA-Z])(?=.*\d)(?=.*\W).{5,20}$/.test(txt)) {
        return true;
    }
    return false;
};
