<template>
  <customHeader title="마이노트" path="note" />
  <q-page-container style="height: 100vh; background-color:#FAFAFA ;">
    <q-scroll-area visible style="height:100%;">
      <div class="page__wrap">
        <div style="display: flex;justify-content: space-between;align-items: center;">
          <div class="title">
            킬러노트 목록
          </div>
          <div>
            <CqBtn label="필터" class="btn__filter" icon="img:/assets/images/icon/icon_filter.svg"
              @onClickFn="() => { modal = !modal }">
            </CqBtn>
          </div>
        </div>
        <div v-if="data">
          <q-card v-for="card in data" flat class="my-card">
            <q-item>
              <q-item-section>
                <q-item-label style=" font-size: 1.75vmin; font-weight: 700;color: #222222;">{{ card.level }}레벨 {{
                    card.lesson
                }}레슨 {{ card.area }}
                </q-item-label>
                <div style="margin-top: 2vmin;">
                  <div v-for='(item, index) in card.areaItems' style="height: 50%;">
                    <div style="display:flex;flex-direction:row;align-items: center; ">
                      <div
                        style="width: 200px;background-color: white; border: 1px solid #BDBDBD; padding: 11px 10px; border-radius: 10px;color:#444; font-size: 1.8vmin;text-align: center; margin:7px 40px 7px 0px;">
                        <span
                          style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;width: 100%;display: block;">
                          지문{{ index + 1 }}{{ headerEtcCd01[item.passageClass] ? '-' + headerEtcCd01[item.passageClass]
                              :
                              ''
                          }}
                        </span>
                      </div>
                      <div style="color:#444;font-size: 2.2vmin;">{{ item.passageName }}</div>
                    </div>
                  </div>
                </div>
              </q-item-section>
              <q-card-actions>
                <div style="color: #8E8E8E;font-size: 1.5vmin; font-weight: 500;">오답노트를 {{ card.cnt }}회 완료 했어요!</div>
                <q-btn flat round style="color: #BDBDBD;font-size: 1.2vmin;" icon="arrow_forward_ios" />
              </q-card-actions>
            </q-item>
          </q-card>
        </div>
        <div v-if="!data" style="height: 60vh;align-items: center;display: grid;">
          <div style="text-align: center;">
            <div style="color: #8E8E8E; font-size: 2.25vmin; font-weight: 400; margin-bottom: 5vmin;">아직 오답노트가 없어요!
            </div>
            <CqBtn unelevated color="primary" label="오늘의 진행 학습 보러가기"
              style="font-size: 1.75vmin; padding: 1.6vmin 6.1vmin;border-radius: 1vmin;" />
          </div>
        </div>
      </div>
    </q-scroll-area>
  </q-page-container>


  <q-dialog v-model="modal" class="modal">
    <q-card class="modal__wrap">
      <q-card-section class="row items-center q-pb-none">
        <q-space />
        <q-btn class="close" icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <div class="modal__content">
        <div class="title">
          <div>
            <b>
              레벨 선택
            </b>
          </div>
          <q-btn flat label="필터초기화"
            style="color:#222222; font-size: 1.5vmin;font-weight: 500; border: 1px solid #8E8E8E;"
            :onClick="onClickClear" />
        </div>
        <q-checkbox class="modal__checkbox" v-model="levelAll" label="모든레벨" :onClick="() => { onClickAll('level') }" />
        <q-option-group inline v-model="levelFilters" :options="levelOptions" color="primary" type="checkbox"
          class="modal__checkbox" />
        <q-separator style="margin-top: 2.6vmin;" />
        <div class="title">
          <div>
            <b>
              갈래/주제 선택
            </b>
          </div>
        </div>
        <q-checkbox class="modal__checkbox" v-model="passageAll" label="모든영역"
          :onClick="() => { onClickAll('passage') }" />
        <q-option-group inline v-model="passageFilters" :options="passageOptions" color="primary" type="checkbox"
          class="modal__checkbox" />
      </div>

      <q-card-actions align="center">
        <CqBtn class="btn" color="primary" size="lg" style="width: 50%;" label="필터 적용하기"
          @keydown.enter="closeAlert(true)" @click="closeAlert(true)" />
      </q-card-actions>
    </q-card>
  </q-dialog>

</template>

<script setup>
import customHeader from "@/components/layouts/header/header.vue";
import SiderLayer from "@/components/layouts/sider/siderLayer.vue";
import CqBtn from "@/components/quasar/cqBtn.vue";
import { headerEtcCd01 } from "@/helpers/qbankParser/qbankMeta";
import { historyBack } from "@/helpers/utils"
import { ref, watch } from "vue";
import { useRouter } from "vue-router";

const router = useRouter();
const modal = ref(false);
const levelFilters = ref([]);
const levelAll = ref(false);
const passageAll = ref(false);
const passageFilters = ref([]);
const levelOptions = [
  {
    label: '1레벨',
    value: '1'
  },
  {
    label: '2레벨',
    value: '2'
  },
  {
    label: '3레벨',
    value: '3'
  },
  {
    label: '4레벨',
    value: '4'
  },
  {
    label: '5레벨',
    value: '5'
  },
  {
    label: '6레벨',
    value: '6'
  },
];
const passageOptions = [
  {
    label: '과학',
    value: '1'
  },
  {
    label: '예술',
    value: '2'
  },
  {
    label: '사회',
    value: '3'
  },
  {
    label: '인문',
    value: '4'
  },
  {
    label: '정치사회',
    value: '5'
  },
  {
    label: '경제문화',
    value: '6'
  },
  {
    label: '기술',
    value: '7'
  },
  {
    label: '융합',
    value: '8'
  },
  {
    label: '현대시',
    value: '9'
  },
  {
    label: '고전시가',
    value: '10'
  },
  {
    label: '현대소설',
    value: '11'
  },
  {
    label: '극수필',
    value: '12'
  },
  {
    label: '고전산문',
    value: '13'
  },
];

const data = [
  {
    level: 1,
    lesson: 1,
    area: '문학',
    cnt: 2,
    areaItems: [
      { passageClass: 12, passageName: '달밤' },
      { passageClass: 12, passageName: '달밤' }
    ]
  },
  {
    level: 1,
    lesson: 2,
    area: '문학',
    cnt: 2,
    areaItems: [
      { passageClass: 12, passageName: '달밤' },
      { passageClass: 12, passageName: '달밤' }
    ]
  },

];

const onClick = (pg) => {
  router.push('/' + pg);
}

const onClickClear = () => {
  passageFilters.value = [];
  levelFilters.value = [];
}

const onClickAll = (type) => {
  console.log(type)
  if (type === 'level') {
    if (levelAll.value) {
      levelFilters.value = levelOptions.map(item => { return item.value });
    } else {
      levelFilters.value = [];
    }
  }
  if (type === 'passage') {
    if (passageAll.value) {
      passageFilters.value = passageOptions.map(item => { return item.value });
    } else {
      passageFilters.value = [];
    }
  }
}

watch(() => levelFilters.value, (newValue) => {
  if (newValue?.length === levelOptions.length) {
    levelAll.value = true;
  } else {
    levelAll.value = false;
  }
});
watch(() => passageFilters.value, (newValue) => {
  if (newValue?.length === passageOptions.length) {
    passageAll.value = true;
  } else {
    passageAll.value = false;
  }
});

</script>

<style scoped lang="scss">
.page__wrap {
  height: 100%;
  background-color: #FAFAFA;
  padding: 3.5vmin 5.2vmin;

  .btn__filter {
    color: #000000;
    font-size: 1.5vmin;
    font-weight: 500 !important;
    border: 2px solid #8E8E8E;
    border-radius: 8px;
    padding: 1vmin 2vmin;

    .q-btn__content {
      .block {
        font-weight: 500 !important;

      }
    }
  }
}



.title {
  color: #222222;
  font-size: 2.1vmin;
  font-weight: 700;
  margin: 2.5vmin 0 2.5vmin;
}

.my-card {
  border: 1px solid #E0E0E0;
  background: #fff;
  border-radius: 1vmin;
  padding: 3.1vmin 4.2vmin;
  margin: 1vmin 0;
}

.modal {
  .title {
    color: #222222;
    font-size: 2.1vmin;
    font-weight: 700;
    margin: 1.75vmin 0vmin 0.5vmin;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__wrap {
    width: 50vw;
    min-width: 500px;
    max-width: 1000px;
    border-radius: 2.5vmin;
  }

  &__content {
    padding: 0vmin 3.5vmin 2vmin;
  }

  &__checkbox ::v-deep {
    font-size: 1.6vmin;
  }

  .q-option-group ::v-deep {
    .q-checkbox__bg {
      border-radius: 50% !important;
    }
  }

  .q-checkbox ::v-deep {
    .q-checkbox__bg {
      border-radius: 50% !important;
    }
  }

  .btn {
    border-radius: 1vmin;
    margin-bottom: 2vmin;
  }

  .close {
    margin: 1.5vmin;
  }

  .q-option-group {
    ::v-deep>div {
      width: 23%;
    }
  }
}
</style>
