const initCoreReading = (rs) => {
  console.log(rs);
  isLoading.value = false;
  // isKreadVisible.value = true;
  // store.openModal(true);
  const tmpEtcInfo = getEtcInfo(rs);
  data.value = {
    content: getHeaderText(rs),
    kread: tmpEtcInfo.kread,
    title: tmpEtcInfo.title,
    etcInfo: tmpEtcInfo,
    standardReadingTime: moment('2022-10-01 00:00:00').add(tmpEtcInfo.solveTime, 's').format('m분ss초'),
    solveTime: tmpEtcInfo.solveTime
  };
  questionHeader.value = getHeader(rs);
  console.log('*************************************************************');
  console.log(tmpEtcInfo.bookNm);
  console.log('*************************************************************');
  bookNm.value = tmpEtcInfo.bookNm;
  startTime = moment();
}


const nextStep = (v = true, no = -1) => {
  soundEffect('next');
  if (dialog.value !== false) {
    // 확인 문제 팝업이 활성화 되었을경우 강제 닫기
    dialog.value = false;
  }
  if (!problems[problemOrder.value]['startDtm']) {
    problems[problemOrder.value]['startDtm'] = moment().format('YYYY-MM-DD HH:mm:ss');
  }
  if (problems[(problemOrder.value)] && problems[(problemOrder.value)]['result'] === null) {
    problems[(problemOrder.value)]['studyTime'] = problems[(problemOrder.value)]['studyTime'] + Math.round(moment.duration(moment().diff(startTime)).asSeconds());
  }
  startTime = moment();
  if (problems[(problemOrder.value)] && problems[(problemOrder.value)]['result'] !== null && problems[(problemOrder.value)]['result']['marking'] !== 'halfWrong') {
    problems[problemOrder.value]['done'] = true;
  }

  if (notiType === 'last' && v) {
    if (isDrill) {
      return saveResult();
      // return router.push('/vocabularylearning');
    } else {

      return saveResult();
      // return router.push('/authenticreading');
    }
  }
  if (no !== -1) {
    problemOrder.value = no;
  } else if (!v) {
    problemOrder.value = problemOrder.value - 1;
  } else {
    problemOrder.value = problemOrder.value + 1;
  }
  questionHeader.value === null && getHeader(problems[(problemOrder.value)].data)
  if (problemOrder.value === 1) {
    notiType = 'first'
    store.openModal(false);
    // questionHeader.value = getHeader(problems[(problemOrder.value)].data);
  } else if (problemOrder.value === Object.keys(problems).length) {
    notiType = 'last'
  } else {
    notiType = 'middle'
  }
