    $q.notify({
      message: '오답은 한 번 다시 풀어보아요.',
      // color: 'secondary',
      position: 'right',
      timeout: 1000
    })
  }
  notiClear();
  setNotiNavigation(problems[problemOrder.value]);
  // notiNav !== null && notiNav();
  // notiNavBack !== null && notiNavBack();
  // notiNav = null;
  // notiNavBack = null;
  // startTime = moment();
  setScrollPosition();
}

const initExams = () => {
  getKrtExam({
