    // version: studyStore.exprVersion, examNm: studyStore.process.exam.examCode 
    version: studyStore.exprVersion, examNm: 'KRT_02_T1'
  }, (rs) => {
    // TODO: store 에 코어리딩 문제 저장
    // rs.examMaps 
    if (rs && rs.examMaps) {
      const exId = rs.examIds[0];
      rs.examMaps[exId].forEach((item, index) => {
        getKrtProblem({
          version: studyStore.exprVersion, problemId: item.problemId
          // version: '1.00.01', problemId: 382222
        }, (r) => {
          headerInfo.value = { ...headerInfo.value, [index + 1]: { answer: null, correctFlag: null } }
          problems = {
            ...problems,
            // TODO: 문항 검수용 전체 문제 보기
