              // [index]: {
              questionHeader: getHeader(r),
              problemNo: index + 1,
              problemId: r.content.problemId,
              // analysisOrd: item.analysisOrd,
              etcInfo: getEtcInfo(r),
              data: r,
              // type: studyStore.process.exam.ln,
              studyTime: 0,
              answer: null,
              reAnswer: null,
              markingType: 'right', //'right': 정답, 'halfWrong':1차오답 , 'halfRight':2차정답 , 'wrong':2차오답
              videoTime: 0,
              relationResult: null,
              // 하단 네비 조정을 위한 변수
              result: null,              // 채점 여부및 1,2차 정오답 
              done: false,               // 채점및 풀이 확인 완료여부
              scrollCheck: false,             // 해설 끝까지 확인 , 스크롤 하단 갔었는지 체크
              // relationId: r.content.property.problem?.relations.length > 0 ? r.content.property.problem?.relations[0].relationId : null,           // 확인 문제 있는지 여부
              completeVideo: false,         // 영상 시청 여부
              // TODO: 큐뱅크 해설영상 파싱필요 
              videoUrl: null,              // 해설 영상 여부
              questionClass: 'BASE',
            }
          }
          if (index === 0) {
            initCoreReading(r);
          }
          problem.value = problems[1];
        });
      });
    }
  });
}
onMounted(() => {
  isLoading.value = true;
  setTimeout(() => { initExams(); }, 1000);
})


const answerNoti = () => {
  notiAnswer !== null && notiAnswer();
  notiAnswer = null;
  if (notiAnswer === null) {
    notiAnswer = $q.notify({
      group: false,
      timeout: 0,
      message: '',
      multiLine: true,
      color: 'secondary',
      classes: 'force-inline',
      position: 'bottom-right',
      actions:
        [{ label: '정답확인', color: 'yellow', handler: () => { notiAnswer = null; setQuestionsResult(); } }]
    });
  }
}

onUnmounted(() => {
  notiClear();
  isLoading.value = true;
  store.openModal(false);
})

const notiClear = () => {
  notif !== null && notif();
  notiAnswer !== null && notiAnswer();
  notiNav !== null && notiNav();
  notiNavBack !== null && notiNavBack();
  notiAdditionalNav !== null && notiAdditionalNav();
  notif = null;
  notiAnswer = null;
  notiNav = null;
  notiNavBack = null;
  notiAdditionalNav = null;
}
// watch(() => problem.value, (newValue) => {
//   // const questionHeader = JSON.parse(JSON.stringify(newValue));
//   console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!' + newValue);
//   const vContent = toRaw(newValue);
//   console.log(vContent);
// });
// watch(() => problem.visibleScoring, (newValue) => {
//   // const questionHeader = JSON.parse(JSON.stringify(newValue));

// });

watch(() => studyStore.process, (newValue) => {
  isLoading.value = true;
  isKreadVisible.value = false;
  setTimeout(() => { initExams(); }, 1000);
});
watch(() => problem, (newValue) => {
  console.log('^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^');
});


const setAnswer = (val) => {
  soundEffect('tic');
  if (problems[problemOrder.value]['result']) {
    problems[problemOrder.value]['reAnswer'] = val;
    console.log(problems[problemOrder.value]['reAnswer']);
    // answerNoti();
    setNotiNavigation(problems[problemOrder.value]);
  } else {
    problems[problemOrder.value]['answer'] = val;
    console.log(problems[problemOrder.value]['answer']);
    const tmpAnswerArr = _.filter(problems, item => { return item.answer === null });
    if (tmpAnswerArr.length === 0) {
      console.log('All Answers !!!!!!!!!!!!!!!');
      isAllChecked = true;
    }
    // setNotiNav(tmpAnswerArr.length === 0);
    setNotiNavigation(problems[problemOrder.value], tmpAnswerArr.length === 0);
    console.log('%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%');
    console.log(problems);
    console.log('%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%');
    headerInfo.value = { ...headerInfo.value, [problemOrder.value]: { answer: val, correctFlag: null } }
  }
  // Object.values(tmpAnswer.value).forEach(item => {
  //   console.log(item);
  //   if (item !== '') { tmpCheck += 1; }
  // });
  // if (tmpCheck >= inputIdx.value) {
  //   answerNoti();
  // } else {
  //   notiAnswer !== null && notiAnswer();
  //   notiAnswer = null;
  // }
  // if (typ) {
  //   console.log(problems[problemOrder.value]);
  //   problem.value = {
  //     ...problem.value, answer: { ...tmpAnswer.value }
  //   }
  // }
}

const setNotiNav = (v = false) => {
  if (!isScored && v) {
    answerNoti();
  }

  if (notiNav === null && (notiType !== 'last' || isScored)) {
    notiNav = $q.notify({
      group: false,
      timeout: 0,
      message: '',
      color: 'blue-6',
      classes: 'force-inline',
      position: 'bottom-right',
      multiLine: true,
      actions:
        notiType === 'last' && isScored ? [
          { label: '개념학습', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }] : [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }]
    })
  }

  if (notiType !== 'first') {
    if (notiNavBack === null) {
      notiNavBack = $q.notify({
        group: false,
        timeout: 0,
        message: '',
        color: 'grey-12',
        classes: 'force-inline',
        position: 'bottom-left',
        multiLine: true,
        actions: [{ label: '', icon: 'arrow_back_ios_new', color: 'grey-6', handler: () => { nextStep(false); } }],
      })
    };
  }
