const setNotiNavigation = (p, allCheck = false) => {
  // notiClear();
  console.log(p);
  if (p.done === true) {
    if (notiType !== 'first') {
      if (notiNavBack === null) notiNavBack = $q.notify({
        group: false,
        timeout: 0,
        message: '',
        color: 'grey-12',
        classes: 'force-inline',
