      if (notiAdditionalNav === null) notiAdditionalNav = $q.notify({
        group: false,
        timeout: 0,
        message: '',
        multiLine: true,
        color: 'red-12',
        classes: 'force-inline',
        position: 'bottom-right',
        actions: [{ label: '해설 영상', color: 'white', handler: () => { videoDialog.value = true; } }]
      })
    }
