<template>
    <!-- <div class="text-box" v-html="headerHtml" v-on:touchstart="customTouchStart" v-on:touchend="customTouchEnd"></div> -->
    <!-- <div id="header_box" class="text-box" :v-bind:on-touchend="test"> -->
    <div style="height:100%; padding:0px 10px 0px 0px; display: flex;">
        <div class="q-gutter-sm q-mr-sm" style="width: 40px;">
            <q-btn round outline :style="`color: ${isPenMode ? 'black' : 'lightgrey'};`"
                :onclick="() => { isPenMode = true }">
                <q-icon name="img:/assets/images/icon/icon_pen.svg" size="25px" />
                <q-tooltip class="text-body2 pen-tooltip" anchor="center right" self="center left" v-model="penTooltip"
                    style="background-color: #EEF051; color:black;" transition-show="jump-right"
                    transition-hide="jump-left">
                    핵심어와 중심 문장의 핵심 내용에 밑줄을 그으며 읽으세요.
                </q-tooltip>
            </q-btn>
            <q-btn round outline :style="`color: ${isPenMode ? 'lightgrey' : 'black'};`"
                :onclick="() => { isPenMode = false }">
                <q-icon name="img:/assets/images/icon/icon_view.svg" size="20px" />
            </q-btn>
        </div>
        <div style="width: calc(100% - 40px); height: 100%;">
            <q-scroll-area visible style="height:100%; padding:0px 10px;">
                <q-chip v-if="bookNm?.indexOf('수능') > -1 || bookNm?.indexOf('교과') > -1">{{ bookNm }}</q-chip>
                <div id="header_box" class="text-box" :class="`${isPenMode ? 'highlighted' : 'highlighted-none'}`"
                    :onmouseup="test" v-html="headerHtml">
                </div>
                <div v-if="headerHtml" v-scroll-fire="readEnd" style="width: 100%; height: 1px;">
                    &nbsp;
                </div>
            </q-scroll-area>
        </div>
    </div>
    <q-menu touch-position :target="menuShow" @before-hide="() => { menuShow = false }">
        <q-list style="min-width: 100px">
            <q-item clickable v-close-popup :onclick="deleteMark">
                <q-item-section>
                    <div style="display: inline-flex;">
                        <div
                            style="width: 20px;height:20px;background-color: yellow;border-radius: 50%; margin-right: 6px;" />
                        <div>하이라이팅 삭제</div>
                    </div>
                </q-item-section>
            </q-item>
            <q-separator />
            <div v-for="n in inputIdx" :key="n">
                <q-item clickable v-close-popup :onclick="() => highlightToAnswer(n)">
                    <q-item-section>답 입력({{ n }}번)</q-item-section>
                </q-item>
            </div>
            <!-- <q-item clickable v-close-popup :onclick="highlightToAnswer">
                <q-item-section>답 입력(2번)</q-item-section>
            </q-item> -->
        </q-list>
    </q-menu>
    <!-- <div class="text-box disableTouch" v-html="headerHtml" @mouseup="test"></div> -->
</template>

<script setup>
import { onMounted, onUnmounted, ref, defineProps, watch, isProxy, toRaw } from 'vue';
import dataToParse from "@/helpers/qbankParser/dataParse";
import { styleTags } from "@/helpers/qbankParser/styleTags";
import { useQuasar } from 'quasar';

const headerHtml = ref('');
const menuShow = ref(false);
const isPenMode = ref(false);
const inputIdx = ref(0);
const bookNm = ref('');
const penTooltip = ref(false);
const $q = useQuasar();

let currentMarkTarget = '';


const emit = defineEmits(['stopNoti', 'setInputAnswer']);

const props = defineProps({
    'questionHeader': {
        default: ''
    },
    'inputIdx': {
        default: 0
    },
    'bookNm': {
        default: ''
    },
});

// TODO: oncontextmenu 테스트 이후 제거
// window.oncontextmenu = function (event) {
//     event.preventDefault();
//     // event.stopPropagation();
//     // return false;
// };

const readEnd = () => {
    emit('stopNoti');
}

const setDirectionTextArea = (text) => {
    let content = '';
    // console.log(text);
    const elements = text.elements;
    if (elements.length > 0) {
        for (const element of elements) {
            // console.log(element);
            Object.entries(element).forEach(([key, value]) => {
                // console.log(key);
                // console.log(value);
                if (key == 'char') {
                    let tempContent = dataToParse.transferStyleTag(value.content);
                    if (tempContent.indexOf('IMAGE') != -1) {
                        tempContent = tempContent.replace(/IMAGE/gi, 'img class="textbox_inputImg"').replace(/path/gi, 'src');
                        // setTempDirectionPath(value);
                        // let paths = tempDirectionPath;
                        // for (const path of paths) {
                        //     tempContent = tempContent.replace(path, qbankAssetUrl + path);
                        // }
                    }
                    content += tempContent;
                } else {
                    setDirectionTextArea(value);
                }
            });
        }
    } else {
        Object.keys(text).map(itemElKey => {
            // console.log(itemElKey);
            // console.log(itemEl);
            if (itemElKey === 'elements') {
                if (itemEl[itemElKey].length > 1) {
                    itemEl[itemElKey].map(elItem => {
                        if (elItem.content === " ") {
                            itemHtml += "<br/>"
                        } else {
                            // console.log(elItem.content);
                            if (elItem.content) {
                                itemHtml += (dataToParse.transferStyleTag(elItem.content));

                            }
                        }
                    })
                } else {
                    if (itemEl.content === " ") {
                        itemHtml += "<br/>"
                    } else {
                        // console.log(itemEl.content);
                        if (itemEl.content) {
                            itemHtml += (dataToParse.transferStyleTag(itemEl.content));
                        }
                    }
                }
            } else if (styleTags[itemElKey] && itemEl[itemElKey] !== null) {
                itemStyle += (styleTags[itemElKey] + ":" + itemEl[itemElKey] + (typeof itemEl[itemElKey] === 'number' ? "px;" : ";"));
            }
        })
        headerHtml.value = (headerHtml.value ? headerHtml.value : "") + `<p style=" ${itemStyle}" >${itemHtml}</p>`;
    }
}

watch(() => props.questionHeader, (newValue) => {
    const questionHeader = toRaw(newValue);
    penTooltip.value = true;
    headerHtml.value = questionHeader;
})
watch(() => props.inputIdx, (newValue) => {
    inputIdx.value = toRaw(newValue);
})
watch(() => props.bookNm, (newValue) => {
    bookNm.value = toRaw(newValue);
})

onMounted(() => {
    const questionHeader = toRaw(props.questionHeader);
    headerHtml.value = questionHeader;
    bookNm.value = toRaw(props.bookNm);
    document.removeEventListener('touchstart', test);
    document.addEventListener('touchstart', test);
    // document.oncontextmenu = function (e) {
    //     alert("오른쪽버튼을 이용할 수 없습니다...");
    //     return false;
    // }
})

onUnmounted(() => {
    // 
    headerHtml.value = '';
    penTooltip.value = false;
})

let markId = 0;
const addMarkEvent = () => {
    const item = document.querySelectorAll('mark');
    item.forEach((target) => {
        target.removeEventListener('click', onMarkTooltipMark);
        target.addEventListener('click', onMarkTooltipMark)
        target.removeEventListener('touchend', onMarkTooltipMark);
        target.addEventListener('touchend', onMarkTooltipMark)
    })
}

const onMarkTooltipMark = (v) => {
    currentMarkTarget = v.target.classList;
    menuShow.value = true;
}
const deleteMark = () => {
    // const val = v.target.classList;
    const val = currentMarkTarget;
    // console.log(val);
    const els = (document.getElementsByClassName(`${val}`));
    // const els = (document.getElementsByClassName(`${'mark_' + val}`));
    //.remove(`${'.mark_' + val}`);
    for (var i = 0; i < els.length; i++) {
        els[i].after(els[i].textContent);
    }
    for (var i = els.length - 1; i > -1; i--) {
        (els[i]).remove();
    }
    currentMarkTarget = '';
}

const highlightToAnswer = (n) => {
    const val = currentMarkTarget;
    // console.log(val);
    const els = (document.getElementsByClassName(`${val}`));
    // const els = (document.getElementsByClassName(`${'mark_' + val}`));
    //.remove(`${'.mark_' + val}`);
    let tmpText = '';
    for (var i = 0; i < els.length; i++) {
        tmpText += els[i].textContent;
    }

    console.log(n);
    console.log(tmpText);
    emit('setInputAnswer', n, tmpText);
}

const test = (e) => {
    // e.preventDefault();
    // alert('!!!!!!!!!!!!!!!!!!!!!!!!');
    // var newNode = document.createElement("strong");
    // newNode.setAttribute("class", "highlighted");
    // console.log(range);
    // console.log(range.commonAncestorContainer);
    // for (let node in range.commonAncestorContainer.childNodes) {
    //     console.log(range.startContainer === node);
    //     console.log(node);
    //     console.log(range.startContainer === range.commonAncestorContainer.childNodes[node]);
    // }
    // range.surroundContents(newNode);
    if (menuShow.value || !isPenMode.value) {
        return false;
    }
    var range = document.getSelection().getRangeAt(0);
    // console.log(document.getSelection().getRangeAt(0));
    if (range.startContainer === range.endContainer) {
        if (range.startOffset !== range.endOffset) {
            markId += 1;
            var highlightelement = document.createElement('mark');
            highlightelement.setAttribute('class', 'mark mark_' + markId);
            // highlightelement.addEventListener("click", function () {
            //     deleteMark(markId);
            // })
            // highlightelement.onclick = () => { deleteMark(markId); };
            range.surroundContents(highlightelement);
            addMarkEvent();
            document.getSelection().removeAllRanges()
        }
    } else {
        var allranges = getSafeRanges(range, document);
        var duCheck = false;
        for (var i = 0; i < allranges.length; i++) {
            if (!duCheck) {
                // if (allranges[i].commonAncestorContainer.innerHTML.indexOf('<mark') > -1 || allranges[i].commonAncestorContainer.innerHTML.indexOf('mark/>') > -1) {
                if (allranges[i].commonAncestorContainer.nodeName === 'MARK') {
                    duCheck = true;
                    break;
                }
            }
        }

        // if ((document.getSelection().getRangeAt(0)?.commonAncestorContainer.innerHTML).indexOf('<mark') > -1 || (document.getSelection().getRangeAt(0)?.commonAncestorContainer.innerHTML).indexOf('mark/>') > -1) {
        if (duCheck) {
            $q.notify({
                color: 'red-4',
                textColor: 'white',
                position: 'center',
                timeout: 1000,
                // icon: 'cloud_done',
                message: '하이라이트는 중복될 수 없습니다. 중복되는 하이라이트를 삭제해주세요.'
            })
            document.getSelection().removeAllRanges();
        } else {

            markId += 1;
            for (var i = 0; i < allranges.length; i++) {
                var highlightelement = document.createElement('mark');
                highlightelement.setAttribute('class', 'mark_' + markId);
                // highlightelement.onclick = () => { deleteMark(markId); };
                // highlightelement.addEventListener("click", function () {
                //     deleteMark(markId);
                // })
                // console.log(allranges[i]);
                allranges[i].surroundContents(highlightelement);
                // console.log(allranges[i]);
                addMarkEvent();
                document.getSelection().removeAllRanges()
            }
        }

    }

};

// var selection = window.getSelection();
// var ranges = selection.getRangeAt(0);
// var allranges = getSafeRanges(ranges, document);
// for (var i = 0; i < allranges.length; i++) {
//     allranges[i].surroundContents(highlightelement);
// }
const getRangeOfMiddleElements = (topStartNode, topEndNode) => {
    var rangeArray = new Array(0);
    for (var i = topStartNode.nextSibling; i != topEndNode; i = i.nextSibling) {
        rangeArray = rangeArray.concat(getRangeOfChildNodes(i));
    }
    return rangeArray;
}

const getRangeOfChildNodes = (node) => {
    var doc = document;
    var rangeArray = new Array(0);
    if (node.nodeType == Node.TEXT_NODE) {
        var xm = doc.createRange();
        xm.setStartBefore(node);
        xm.setEndAfter(node);
        rangeArray.push(xm);
    } else {
        for (var i = 0; i < node.childNodes.length; i++) {
            var childNode = node.childNodes[i];
            rangeArray = rangeArray.concat(getRangeOfChildNodes(childNode));
        }
    }
    return rangeArray;
}

const getSafeRanges = (range, currentDoc) => {
    var doc = currentDoc;
    var commonAncestorContainer = range.commonAncestorContainer;
    var startContainer = range.startContainer;
    var endContainer = range.endContainer;
    var startArray = new Array(0),
        startRange = new Array(0);
    var endArray = new Array(0),
        endRange = new Array(0);
    // @@@@@ If start container and end container is same
    if (startContainer == endContainer) {
        return [range];
    } else {
        for (var i = startContainer; i != commonAncestorContainer; i = i.parentNode) {
            startArray.push(i);
        }
        for (var i = endContainer; i != commonAncestorContainer; i = i.parentNode) {
            endArray.push(i);
        }
    }
    if (0 < startArray.length) {
        for (var i = 0; i < startArray.length; i++) {
            if (i) {
                var node = startArray[i - 1];
                while ((node = node.nextSibling) != null) {
                    startRange = startRange.concat(getRangeOfChildNodes(node));
                }
            } else {
                var xs = doc.createRange();
                var s = startArray[i];
                var offset = range.startOffset;
                var ea = (startArray[i].nodeType == Node.TEXT_NODE) ? startArray[i] : startArray[i].lastChild;
                xs.setStart(s, offset);
                xs.setEndAfter(ea);
                startRange.push(xs);
            }
        }
    }
    if (0 < endArray.length) {
        for (var i = 0; i < endArray.length; i++) {
            if (i) {
                var node = endArray[i - 1];
                while ((node = node.previousSibling) != null) {
                    endRange = endRange.concat(getRangeOfChildNodes(node));
                }
            } else {
                var xe = doc.createRange();
                var sb = (endArray[i].nodeType == Node.TEXT_NODE) ? endArray[i] : endArray[i].firstChild;
                var end = endArray[i];
                var offset = range.endOffset;
                xe.setStartBefore(sb);
                xe.setEnd(end, offset);
                endRange.unshift(xe);
            }
        }
    }
    var topStartNode = startArray[startArray.length - 1];
    var topEndNode = endArray[endArray.length - 1];
    var middleRange = getRangeOfMiddleElements(topStartNode, topEndNode);
    startRange = startRange.concat(middleRange);
    var response = startRange.concat(endRange);
    // console.log(response)
    return response;
}

</script>

