<template>
    <q-slide-transition>
        <q-card v-if="visible" style="width: 50%; max-width: 100%;">
            <q-card-section>
                <div class="row">
                    <div class="col">
                        <q-chip style="background-color: #00A2E0;" text-color="white">확인문제</q-chip>
                    </div>
                    <div class="col" style="text-align: end;">
                        <q-btn flat icon="close" :onClick="() => { emit('onClose') }" />
                    </div>
                </div>
                <div>
                    <div>
                        <div>
                            <AnswerMarking :marking-class="markingClass" style="top:45px" />
                            <v-runtime-template :template="question"></v-runtime-template>
                        </div>
                    </div>
                    <br />
                    <div style="width:100%;">
                        <q-radio :disable="disable" v-if="choiceArrange" v-for="(item, index) in choiceArrange.data"
                            class="col"
                            style="padding: 10px; border-radius: 10px;border: 1px solid #E0E0E0;justify-content: center;margin: 0px 5px 5px;"
                            v-model="choice" :val="item.numChar"
                            v-bind:class="item.numChar === choice ? 'item-select' : 'item-nomal'"
                            @update:model-value="onChangeAnswer">
                            <div v-html="item.text"></div>
                        </q-radio>
                    </div>
                    <br />
                    <br />
                    <br />
                </div>
            </q-card-section>
        </q-card>
    </q-slide-transition>
    <!-- <q-option-group v-model="choice" :options="options" color="red" /> -->
</template>

<script setup>
import { onMounted, onUnmounted, ref, defineEmits, defineProps, watch, toRaw } from 'vue';
import { getQuestion_old, getChoice, getAnswerDetail } from "@/helpers/qbankParser/qbankDataParse";
import { useQuasar } from 'quasar';
import VRuntimeTemplate from "vue3-runtime-template";
import AnswerMarking from './answerMarking.vue';
import { getKrtProblem } from '@/api/modules/qbank';

const question = ref('');
const choiceArrange = ref([]);
const choice = ref('');
const markingClass = ref('');
const correctAnswer = ref([]);
const disable = ref(false);
const visible = ref(false);
let noti = null;

const emit = defineEmits(['setRelationAnswer', 'onClose']);
const $q = useQuasar();

const props = defineProps({
    'pContent': {
        default: {}
    },
});


const onChangeAnswer = (v, evt) => {
    // emit('setAnswer', v);
    if (correctAnswer.value.includes(choice.value)) {
        markingClass.value = 'marking-right';
        // noti !== null && noti();
        noti = $q.notify({
            group: true,
            //    timeout: 5,
            // message: '정답이에요! 확인해야 할 개념 영상이 있어요',
            // TODO: 영상있을시 추가 메세지 작업
            message: '정답이에요!',
            // color: 'secondary',
            classes: 'force-inline',
            position: 'right',
            multiLine: true,
            timeout: 1000
            //    actions: [{ label: '틀렸어요', icon: 'arrow_back_ios', color: 'yellow', handler: () => { nextStep(false); } }],
        })
        disable.value = true;
        emit('setRelationAnswer', choice.value);
    } else {
        markingClass.value = 'marking-halfWrong';
        // noti !== null && noti();
        noti = $q.notify({
            group: true,
            //    timeout: 5,
            message: '틀렸어요! 다시 한번 확인해볼까요?',
            // color: 'secondary',
            classes: 'force-inline',
            position: 'right',
            multiLine: true,
            timeout: 1000
            //    actions: [{ label: '틀렸어요', icon: 'arrow_back_ios', color: 'yellow', handler: () => { nextStep(false); } }],
        })
    }
}

const init = (newValue) => {
    const vContent = toRaw(newValue);
    console.log(vContent)
    const relationId = vContent.data.content.property.problem?.relations?.length > 0 ? vContent.data.content.property.problem.relations[0].relationId : null;

    if (relationId) {
        getKrtProblem({
            version: '1.00.01', problemId: relationId
            // version: '1.00.01', problemId: 382222
        }, (r) => {
            console.log(r);
            question.value = getQuestion_old(r);
            choiceArrange.value = getChoice(r);
            correctAnswer.value = getAnswerDetail(r);
            visible.value = true;
        });
    }
}

const onClose = () => {
    emit('onClose')
}
watch(() => props.pContent, (newValue) => {
    visible.value = false;
    init(newValue);
});

onMounted(() => {
    init(props.pContent);
})

onUnmounted(() => {
    visible.value = false;
    // 
})
</script>

<style lang="scss" scoped>
::v-deep p {
    margin: 0px;
}

::v-deep .q-radio__inner {
    display: none;
}

::v-deep .item {
    &-nomal {
        // background-color: #efefef;
        border-radius: 10px;

        .item-num {
            border-radius: 20px;
            border: 1px solid lightgray;
            padding: 0px 8px 2px;
        }
    }

    &-select {
        background-color: #efefef;
        border-radius: 10px;

        .item-num {
            border-radius: 20px;
            background-color: lightgray;
            padding: 0px 8px 2px;
        }
    }


    &-correct {
        background-color: #d7eaff;
        border-radius: 10px;

        .item-num {
            background-color: #6aaefc;
            border-radius: 20px;
            padding: 0px 8px 2px;
        }
    }

    &-wrong {
        background-color: #ffc8d6;
        border-radius: 10px;

        .item-correct_num {
            background-color: #fc6a8f;
            border-radius: 20px;
            padding: 0px 8px 2px;
        }
    }
}
</style>
