<template>
    <q-dialog v-model="props.visible" @hide="closeModal" persistent class="confirm">
        <q-card class="q-dialog-plugin" :style="'width:' + props.width + ';'">
            <template v-if="props.close || props.title">
                <q-card-section class="row items-center q-pb-none" style="padding: 2vmin 2vmin 1vmin;">
                    <div style="font-size: 1.25vmin; font-weight: 700;">{{ props.title }}</div>
                    <q-space />
                    <q-btn v-if="props.close" class="close" icon="close" flat round dense v-close-popup
                        :onClick="closeModal" />
                </q-card-section>
                <q-separator />
            </template>
            <div style="padding: 2.5vmin"
                :style="(!store.confirmOption.close && !props.title) && 'margin-top: 1.8vmin;'">
                <slot></slot>
            </div>
        </q-card>
    </q-dialog>
</template>

<script setup>
import { computed, onMounted } from 'vue';
// import { useStore } from 'vuex';
import { DialogStore } from "@/store/modules/dialog";
import { defineProps } from 'vue';
import { soundEffect } from '@/helpers/soundEffect';


const store = DialogStore();
const props = defineProps({
    'width': {
        type: String,
        default: "580px"
    },
    'showClose': {
        type: Boolean,
        default: false
    },
    'visible': false,
    'close': false,
    'title': '',
})

// close 사용시에는 visible 변경하는 함수 필수로
const emit = defineEmits(['update:visible'])

const closeModal = () => {
    console.log('close modal layer');
    // store.commit('openModal', false);
    // store.openModal(false);
    emit('update:visible', false)
};

onMounted(() => {
    // soundPopup();
    soundEffect('popup');
})
</script>

<style scoped lang="scss">
.confirm {
    .q-card {
        // width: 580px;
        max-width: 580px;

        border-radius: 2.5vmin;
    }

    &__message {
        font-size: 2.12vmin;
        font-weight: 700;
        text-align: center;
        padding: 0.4vmin 1.87vmin 3.75vmin;
    }

    &__btn {
        .q-btn {
            border-radius: 1vmin;
            padding: 2vmin 0vmin;
            font-size: 1.5vmin;
            font-weight: 700 !important;

            .block {
                font-size: 1.5vmin;
                font-weight: 700 !important;

            }
        }
    }
}
</style>
