    console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    console.log(r.examIds[0]);
    // const tmpHeaderData = (r.header).filter(i => { return i.headerId === rs.content.headerId }).pop();
    const tmpHeaderData = (r.examMaps[r.examIds[0]]).filter(i => { return i.problemId === rs.content.problemId }).pop();

    console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    console.log(tmpHeaderData);
    console.log(rs);
    isLoading.value = false;
    isKreadVisible.value = true;

    // store.openModal(true);
    const tmpEtcInfo = getEtcInfo(rs);
    data.value = {
      content: getHeaderText(rs),
      kread: tmpEtcInfo.kread,
      title: tmpEtcInfo.title,
      etcInfo: tmpEtcInfo,
      standardReadingTime: moment('2022-10-01 00:00:00').add(tmpEtcInfo.solveTime, 's').format('m분ss초'),
