    elapsedTime: 0,
    answer: {},
    reAnswer: null,
    result: null,
    solvingStartTs: moment().format('YYYY-MM-DD HH:mm:ss'),
    solvingEndTs: null,
    questionClass: 'BASE'
  }
}

const nextStep = (v = true) => {
  soundEffect('next');
  // problems[(problemOrder.value)]['elapsedTime'] = problems[(problemOrder.value)]['elapsedTime'] + moment.duration(moment().diff(startTime)).asSeconds();
  startTime = moment();
  if (notiType === 'last' && v) {
    // 학습 완료
    // 학습 데이터 저장
    return saveResult();
    // return router.push('/authenticreading');
  }
  if (!v) {
    problemOrder.value = problemOrder.value - 1;
  } else {
    problemOrder.value = problemOrder.value + 1;
  }
  if (problems[problemOrder.value]['solvingStartTs'] === null) {
    problems[problemOrder.value]['solvingStartTs'] = moment().format('YYYY-MM-DD HH:mm:ss');
  }
  if (problemOrder.value === 1) {
    notiType = 'first'
    store.openModal(false);
    console.log(problems);
    questionHeader.value = getHeader(problems[(problemOrder.value)].data);
  } else if (problemOrder.value === Object.keys(problems).length - 1) {
