  if (inputTypes.includes(problems[problemOrder.value].data.content.property.problem.typeCd)) {
    inputIdx.value = problems[problemOrder.value].data.content.solution.answer.texts.length;
  }
  notiNav !== null && notiNav();
  notiNavBack !== null && notiNavBack();
  notiNav = null;
  notiNavBack = null;
  tmpAnswer.value = {};
  // startTime = moment();
  console.log('************************************************************');
  console.log(problems);
  console.log('************************************************************');
}

