              solvingStartTs: null,
              solvingEndTs: null,
              questionClass: 'BASE',
              rightAnswer: r.content.solution.answer.detail,
            }
          }
          if (index === 0) {
            initCoreReading(r);
            setProblem(r);
          }
        });
      });
    }
  });
}

