  store.openModal(false);
  cornerTime.passageReadStartTs = moment().format('YYYY-MM-DD HH:mm:ss');
  cornerTime.cornerLearningStartTs = moment().format('YYYY-MM-DD HH:mm:ss');
  cornerLearningBaseTime = moment();

  // 지문학습 시작
  postLessonStart({
    level: studyStore.mapTree.level,
    lesson: studyStore.mapTree.lesson,
    areaCode: studyStore.process.exam.areaCode,
    examName: studyStore.process.exam.examCode + '_C',
