  // diffTime.value = Math.round(moment.duration(baseTime.diff(moment())).asSeconds());
  diffTime.value = diffTime.value + (data.value.solveTime - moment.duration(moment().diff(startTime)).asSeconds());
  problems[problemOrder.value]['solvingEndTs'] = moment().format('YYYY-MM-DD HH:mm:ss');
  notif = null
  cornerTime.passageReadEndTs = moment().format('YYYY-MM-DD HH:mm:ss');
  cornerTime.passageReadTime = cornerTime.passageReadTime + moment.duration(moment().diff(startTime)).asSeconds();
  problems[problemOrder.value]['elapsedTime'] = cornerTime.passageReadTime;
}

const setSolveTimeMessage = () => {
  let rsMsg = '';
  const tmpTime = Math.round(diffTime.value);
  if (data.value.solveTime < 60) {
    if (tmpTime > 0) {
      rsMsg = (tmpTime + '초 빨리');
    } else {
      rsMsg = ((tmpTime * -1) + '초 늦게');
    }
  } else {
    if (tmpTime > 30) {
      rsMsg = (tmpTime + '초 빨리');
    } else if (tmpTime > 0) {
      rsMsg = (tmpTime + '초 빨리');
    } else {
      rsMsg = ((tmpTime * -1) + '초 늦게');
    }
  }
  return rsMsg;
};

