const setInputAnswer = (id, val, typ = true) => {
  soundEffect('tic');
  tmpAnswer.value = { ...tmpAnswer.value, [id]: val };
  if (problems[problemOrder.value]['result'] != null) {
    problems[problemOrder.value]['reAnswer'] = tmpAnswer.value;
  } else {
    problems[problemOrder.value]['answer'] = tmpAnswer.value;
  }
  let tmpCheck = 0;
  Object.values(tmpAnswer.value).forEach(item => {
    console.log(item);
    if (item !== '') { tmpCheck += 1; }
  });
  console.log(tmpCheck);
  console.log(inputIdx.value);
  if (tmpCheck >= inputIdx.value) {
    answerNoti();
  } else {
    notiAnswer !== null && notiAnswer();
    notiAnswer = null;
  }
  if (typ) {
    console.log(problems[problemOrder.value]);
    problem.value = {
      ...problem.value, answer: { ...tmpAnswer.value }
    }
  }
}

const setEsasyQuestionsResult = () => {
  soundEffect('result');
  const setNotiNav = () => {
    if (notiNav === null) {
      notiNav = $q.notify({
        group: false,
        timeout: 0,
        message: '',
        color: 'blue-6',
        classes: 'force-inline',
        position: 'bottom-right',
        multiLine: true,
        actions:
          notiType === 'last' ? [
            // { label: '개념학습', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }] : [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }]
            { label: '다음문제', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }] : [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'white', handler: () => { nextStep(); } }]
      })
    }
    if (notiType !== 'first') {
      if (notiNavBack === null) {
        notiNavBack = $q.notify({
          group: false,
          timeout: 0,
          message: '',
          color: 'grey-12',
          classes: 'force-inline',
          position: 'bottom-left',
          multiLine: true,
          actions: [{ label: '', icon: 'arrow_back_ios_new', color: 'grey-6', handler: () => { nextStep(false); } }],
        })
      };
    }
  }
  if (problems[problemOrder.value]['solvingEndTs'] === null) {
    problems[problemOrder.value]['solvingEndTs'] = moment().format('YYYY-MM-DD HH:mm:ss');
  }
  if (problems[problemOrder.value]['result'] == null) {
    problems[(problemOrder.value)]['elapsedTime'] = problems[(problemOrder.value)]['elapsedTime'] + moment.duration(moment().diff(startTime)).asSeconds();
  }
  // problems.value[problemOrder.value]
  const tmpProblem = problems[problemOrder.value];
  let isCorrectAll = true;
  const answerIdx = problems[problemOrder.value]['result'] != null ? 2 : 1;
  //problems[problemOrder.value].data.content.solution.answer.detail
  if (tmpProblem.data.content.solution.answer.detail) {
    const correctArr = [];
    // tmpProblem.data.content.solution.answer.detail.map(item => {
    //   // Object.entries(problems[problemOrder.value]['answer']).forEach(([key, value]) => {
    //   // let tmpUserAnswer = value;
    //   let tmpUserAnswer = problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'][item.ord];
    //   let tmpanswer = item['answer'];
    //   // 공백제거후 비교
    //   if (item.ignorespace === 'Y') {
    //     tmpUserAnswer = tmpUserAnswer.replaceAll(' ', '');
    //     tmpanswer = tmpanswer.replaceAll(' ', '');
    //   }
    //   // 대소문자 구분후 비교
    //   if (item.chkcase === 'Y') {
    //     tmpUserAnswer = tmpUserAnswer.toLowerCase();
    //     tmpanswer = tmpanswer.toLowerCase();
    //   }
    //   if (tmpUserAnswer === tmpanswer) {
    //     correctArr.push({ isCorrect: true, answer: problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'][item.ord] })
    //   } else {
    //     isCorrectAll && (isCorrectAll = false);
    //     correctArr.push({ isCorrect: false, answer: problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'][item.ord] })
    //   }
    //   // });
    // })

    let tmpUserAnswers = problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'];

    Object.entries(tmpUserAnswers).forEach(([key, value]) => {
      let tmpUserAnswer = value;
      let tmpCorrect = false;
      console.log('!!!!!!!!!!!!!!!!!!!! ');
      console.log(tmpUserAnswer);
      console.log(key);
      tmpProblem.data.content.solution.answer.detail.forEach(item => {
        console.log(item);
        console.log(item.ord === Number(key));
        if (item.ord === Number(key) && !tmpCorrect) {
          let tmpanswer = item['answer'];
          if (item.ignorespace === 'Y') {
            tmpUserAnswer = tmpUserAnswer.replaceAll(' ', '');
            tmpanswer = tmpanswer.replaceAll(' ', '');
          }
          // 대소문자 구분후 비교
          if (item.chkcase === 'Y') {
            tmpUserAnswer = tmpUserAnswer.toLowerCase();
            tmpanswer = tmpanswer.toLowerCase();
          }
          console.log(tmpUserAnswer + ' : ' + tmpanswer);
          console.log(tmpUserAnswer === tmpanswer);
          if (tmpUserAnswer === tmpanswer) {
            tmpCorrect = true;
          }
        }
      })
      if (tmpCorrect) {
        correctArr.push({ isCorrect: true, answer: problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'][key] })
      } else {
        isCorrectAll && (isCorrectAll = false);
        correctArr.push({ isCorrect: false, answer: problems[problemOrder.value][answerIdx === 2 ? 'reAnswer' : 'answer'][key] })
      }
    });


    if ((answerIdx === 1 && isCorrectAll) || problemOrder.value === 2) {
      problems[problemOrder.value]['correctFlag'] = 'Y';
      problems[problemOrder.value]['result'] = { marking: problemOrder.value === 2 ? '' : 'right', answer: correctArr };
      setNotiNav();
    } else if (answerIdx === 1 && !isCorrectAll) {
      problems[problemOrder.value]['correctFlag'] = 'N';
      problems[problemOrder.value]['result'] = { marking: 'halfWrong', answer: correctArr }
      $q.notify({
        message: '오답은 한번 다시 풀 수 있어요.',
        timeout: 1000,
        // color: 'secondary',
        position: 'right'
      })
    } else if (answerIdx === 2 && isCorrectAll) {
      problems[problemOrder.value]['reCorrectFlag'] = 'Y';
      problems[problemOrder.value]['result'] = { marking: 'halfRight', answer: correctArr };
      setNotiNav();
    } else if (answerIdx === 2 && !isCorrectAll) {
      problems[problemOrder.value]['reCorrectFlag'] = 'N';
      problems[problemOrder.value]['result'] = { marking: 'wrong', answer: correctArr };
      setNotiNav();
    }
    console.log('*********************************************************************************');
    console.log(problems[problemOrder.value]['result']);
    console.log('*********************************************************************************');
    notiAnswer = null;
    // problem.value = problems[problemOrder.value];
    problem.value = { ...problems[problemOrder.value] };
    console.log(problem);
  }
}

const saveResult = () => {
  let tmpRs = [];
  Object.entries(problems).forEach(([key, value]) => {
    if (key !== '0') {
      let tmpVal = { ...value };

      // tmpVal.rightAnswer = JSON.stringify(tmpVal.rightAnswer);
      tmpVal.answer = JSON.stringify(tmpVal.answer);
      tmpVal.reAnswer = tmpVal.reAnswer ? JSON.stringify(tmpVal.reAnswer) : tmpVal.reAnswer;
      tmpVal.correct = JSON.stringify(tmpVal.rightAnswer)
      tmpVal.elapsedTime = tmpVal.elapsedTime * 1000;

      delete tmpVal.done;
      delete tmpVal.data;
      delete tmpVal.result;
      delete tmpVal.rightAnswer;
      tmpRs.push(tmpVal);
    }
  })
  console.log(studyStore.process.exam.learningType);
  const saveData = {
    examName: studyStore.process.exam.examCode + '_C',
    // examNm: 'KRT_01_02_L_01',
    exprVersion: studyStore.exprVersion,
    // learningType: studyStore.process.exam.learningType,
    learningType: "NORMAL",
    learningHistorySeq: studyStore.process.learningHistorySeq,
    cornerTime: { ...cornerTime, passageReadTime: cornerTime.passageReadTime * 1000, cornerLearningEndTs: moment().format('YYYY-MM-DD HH:mm:ss'), cornerLearningTime: (moment.duration(moment().diff(cornerLearningBaseTime)).asSeconds()) * 1000 },
    submission: tmpRs
  }
  console.log('saveData %%%%%%%%%%%%%%%%%%%%%%%%%');
  console.log(JSON.stringify(saveData));
  postAnswer({ type: 'core', data: saveData }, () => {
    router.push('/authenticreading');
    studyStore.process.studyTree = studyStore.process.studyTree + 1;
    postUserJson(JSON.stringify(studyStore.process), (rs) => {
      //
    });
  })
}

