const setQuestionsResult = () => {
  soundEffect('result');
  notiAdditionalNav = null;
  if (isDrill) {
    const tmpIsRight = problems[problemOrder.value].data.content.solution.answer.detail[0].answer === problems[problemOrder.value].answer;
    if (problems[problemOrder.value]['result']) {
      problems[problemOrder.value]['result'] = { marking: tmpIsRight ? 'halfRight' : 'wrong', answer: problems[problemOrder.value].answer }
      problem.value = { ...problems[problemOrder.value] };
    } else {
      if (!problems[problemOrder.value]['endDtm']) {
        problems[problemOrder.value]['endDtm'] = moment().format('YYYY-MM-DD HH:mm:ss');
      }
      console.log('setQuestionsResult !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! 000');
      console.log(problems[problemOrder.value]['questionClass']);
      console.log(tmpIsRight);
      if (problems[problemOrder.value]['questionClass'] === 'DRILL_UP' && !tmpIsRight) {
        let tmpArr = {};
        let idx = 1;
        Object.entries(problems).forEach(([key, i]) => {
          if (i.analysisOrd !== problems[problemOrder.value].analysisOrd || Number(i.etcInfo.difficultCd) <= Number(problems[problemOrder.value].data.content.property.analysis.difficultCd)) {
            tmpArr = { ...tmpArr, [idx]: { ...i, problemNo: idx } };
            idx += 1;
          }
        });
        console.log('setQuestionsResult !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! 111');
        console.log(tmpArr);
        problems = tmpArr;
        // problems = problems.filter(i => { return (i.analysisOrd !== problem.analysisOrd || Number(i.data.content.property.analysis.difficultCd) < Number(problems[item.analysisOrd].data.content.property.analysis.difficultCd)) });
      } else if (problems[problemOrder.value]['questionClass'] === 'DRILL_DOWN' && tmpIsRight) {
        let tmpArr = {};
        let idx = 1;
        Object.entries(problems).forEach(([key, i]) => {
          if (i.analysisOrd !== problems[problemOrder.value].analysisOrd || Number(i.data.content.property.analysis.difficultCd) >= Number(problems[problemOrder.value].data.content.property.analysis.difficultCd)) {
            tmpArr = { ...tmpArr, [idx]: { ...i, problemNo: idx } };
            idx += 1;
          }
        });
        console.log('setQuestionsResult !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! 2222');
        console.log(tmpArr);
        problems = tmpArr;
        // problems = problems.filter(i => { return (i.analysisOrd !== problem.analysisOrd || Number(i.data.content.property.analysis.difficultCd) > Number(problems[item.analysisOrd].data.content.property.analysis.difficultCd)) });
      }
      console.log('setQuestionsResult !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
      console.log(problems);
      problems[problemOrder.value]['result'] = { marking: problems[problemOrder.value].data.content.solution.answer.detail[0].answer === problems[problemOrder.value].answer ? 'right' : 'halfWrong', answer: problems[problemOrder.value].answer }
      problem.value = { ...problems[problemOrder.value] };
      if (problemOrder.value >= Object.keys(problems).length) {
        notiType = 'last';
      }
    }
  } else {

    if (isScored) {
      problems[problemOrder.value]['result'] = { marking: problems[problemOrder.value].data.content.solution.answer.detail[0].answer === problems[problemOrder.value].answer ? 'halfRight' : 'wrong', answer: problems[problemOrder.value].answer }
      problem.value = { ...problems[problemOrder.value] };
    } else {
      isAllChecked = false;
      if (problems[(problemOrder.value)]['result'] === null) {
        problems[(problemOrder.value)]['studyTime'] = problems[(problemOrder.value)]['studyTime'] + Math.round(moment.duration(moment().diff(startTime)).asSeconds());
        if (!problems[problemOrder.value]['endDtm']) {
          problems[problemOrder.value]['endDtm'] = moment().format('YYYY-MM-DD HH:mm:ss');
        }
      }
      startTime = moment();
      const tmpData = [];
      let tmpDrillProblems = [];
      Object.entries(problems).forEach(([key, item]) => {
        const tmpIsRight = item.data.content.solution.answer.detail[0].answer === item.answer;
        if (tmpIsRight) {
          tmpData.push({ no: item.analysisOrd, marking: 'right', analysis: item.data.content.property.analysis.analysisNm, difficult: difficult[item.data.content.property.analysis.difficultCd].label })
          tmpDrillProblems = [...tmpDrillProblems, ...allProblems.filter(i => { return i.analysisOrd === item.analysisOrd && Number(i.difficultCd) > Number(item.data.content.property.analysis.difficultCd) })];
        } else {
          let tmpArr = _.sortBy([...allProblems.filter(i => { return i.analysisOrd === item.analysisOrd && Number(i.difficultCd) < Number(item.data.content.property.analysis.difficultCd) })], 'difficultCd').reverse();
          tmpData.push({ no: item.analysisOrd, marking: 'halfWrong', analysis: item.data.content.property.analysis.analysisNm, difficult: difficult[item.data.content.property.analysis.difficultCd].label })
          tmpDrillProblems = [...tmpDrillProblems, ...tmpArr];
        }
        item.result = { marking: tmpIsRight ? 'right' : 'halfWrong', answer: item.answer };
      });
      const total = _.countBy(tmpData, (i) => {
        return i.marking === 'right' ? 'right' : 'wrong';
      });
      resultData.value = { ...total, data: tmpData };
      console.log('allProblems!!!!!!!!!!!!!!!!');
      console.log(tmpDrillProblems);
      tmpDrillProblems.forEach((item, index) => {
        getKrtProblem({
          version: '1.00.01', problemId: item.problemId
          // version: '1.00.01', problemId: 382222
        }, (r) => {
          drillProblems = {
            ...drillProblems, [index + 1]: {
              problemNo: index + 1,
              problemId: r.content.problemId,
              analysisOrd: item.analysisOrd,
              etcInfo: getEtcInfo(r),
              data: r,
              studyTime: 0,
              answer: null,
              reAnswer: null,
              markingType: 'right', //'right': 정답, 'halfWrong':1차오답 , 'halfRight':2차정답 , 'wrong':2차오답
              videoTime: 0,
              relationResult: null,
              // correctiveStatus: false,
              // totalCnt: 0,
              // process: 1,
              // 하단 네비 조정을 위한 변수
              result: null,              // 채점 여부및 1,2차 정오답 
              done: false,               // 채점및 풀이 확인 완료여부
              scrollCheck: false,             // 해설 끝까지 확인 , 스크롤 하단 갔었는지 체크
              relationId: r.content.property.problem?.relations.length > 0 ? r.content.property.problem?.relations[0].relationId : null,           // 확인 문제 있는지 여부
              completeVideo: false,         // 영상 시청 여부
              // TODO: 큐뱅크 해설영상 파싱필요 
              videoUrl: null,              // 해설 영상 여부
              // drill: problems[item.analysisOrd].result.marking === 'right' ? 'up' : 'down',
              prevLabel: difficult[problems[item.analysisOrd].data.content.property.analysis.difficultCd].label,
              questionClass: problems[item.analysisOrd].result.marking === 'right' ? 'DRILL_UP' : 'DRILL_DOWN',
              baseDifficult: problems[item.analysisOrd].etcInfo.difficultCd
            }
          }
          console.log('drillProblems!!!!!!!!!!!!!!!!');
          console.log(drillProblems);
        });
      });
      isScored = true;
      notiClear();
      // problem.value = problems[problemOrder.value];
      // problemOrder.value = 0;
      // problem.value = problems[0];
      // notiType = 'first'
      // resultDialog.value = true;
      store.openModal(true);
    }
  }
}

