<template>
    <!-- <q-layout view="hHh lpR fFf"> -->
    <q-layout style="overflow-y: hidden;">
        <q-ajax-bar ref=" bar" size="5px" :delay="delay" />
        <!-- <StudyHeader v-if="menuMeta?.header === true" /> -->
        <router-view />
        <!-- <alertDialog v-if="alertOption.open" /> -->
        <!-- <confirmDialog v-if="confirmOption.open" /> -->
        <alertDialog />
        <confirmDialog />
    </q-layout>
</template>

<script>
import { computed, watch } from 'vue';
// import { useStore } from 'vuex';
import { DialogStore } from "@/store/modules/dialog";
import { AuthStore } from "@/store/modules/auth";
import alertDialog from '@/components/layouts/dialog/alertDialog';
import confirmDialog from '@/components/layouts/dialog/confirmDialog';
import StudyHeader from './components/layouts/header/studyHeader.vue';


const setScreenSize = () => {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

window.removeEventListener('click', setScreenSize);
window.addEventListener('resize', setScreenSize);

export default {
    name: 'app',
    components: {
        alertDialog,
        confirmDialog,
        StudyHeader
    },
    setup() {
        const delay = 0;
        const store = DialogStore();
        const authStore = AuthStore();

        const menuMeta = computed(() => authStore.menuMeta);
        // const alertOption = computed(() => store.alertOption);
        // const confirmOption = computed(
        //     () => store.confirmOption,
        // );
        const onStartAjaxBar = () => {
            this.$refs.bar.start()
        }
        const onStopAjaxBar = () => {
            this.$refs.bar.stop()
        }
        setScreenSize();
        return {
            delay,
            onStartAjaxBar,
            onStopAjaxBar,
            menuMeta,
            // confirmOption,
            // alertOption,
        };
    },
};
</script>
