<template>
    <!-- <q-slide-transition> -->
    <div class="answer-box" v-if="visible">
        <div><b>정답</b></div>
        <!-- <p v-html="answer.detail"></p> -->
        <p v-html="answer.texts"></p>
        <div v-if="answer.explain"><b>해설</b></div>
        <p v-html="answer.explain"></p>
        <div v-if="answer.hint"><b>오답피하기</b></div>
        <p v-html="answer.hint"></p>
        <div id="observerDiv" class="example-observed text-center rounded-borders">
            &nbsp;
        </div>
    </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref, defineProps, watch, toRaw } from 'vue';
import { getAnswer, getEtcInfo } from "@/helpers/qbankParser/qbankDataParse";
import cqBtn from "@/components/quasar/cqBtn.vue"
import { color } from 'd3-color';
import VideoTest from '@/views/home/components/VideoTest.vue';
import problemRelation from "@/components/qbank/problemRelation.vue";
import { getKrtProblem } from '@/api/modules/qbank';
import { useQuasar } from 'quasar';

const answer = ref('');
const visible = ref(false);
const dialog = ref(false);
const videoDialog = ref(false);
const rProblem = ref(null);
const intersection = ref(false);
const noti = null;
const $q = useQuasar();
let io = null;

const emit = defineEmits(['onIntersection']);

const props = defineProps({
    'pContent': {
        default: {}
    },
    'answerNoti': false,
});

const setAnswer = (val) => {
    soundEffect('tic');
    rProblem['answer'] = val;
    console.log(rProblem['answer']);
    // const tmpAnswerArr = _.filter(problems, item => { return item.answer === null });
    // if (tmpAnswerArr.length === 0) {
    //     console.log('All Answers !!!!!!!!!!!!!!!');
    // }
    // setNotiNav(tmpAnswerArr.length === 0);
    // Object.values(tmpAnswer.value).forEach(item => {
    //   console.log(item);
    //   if (item !== '') { tmpCheck += 1; }
    // });
    // if (tmpCheck >= inputIdx.value) {
    //   answerNoti();
    // } else {
    //   notiAnswer !== null && notiAnswer();
    //   notiAnswer = null;
    // }
    // if (typ) {
    //   console.log(problems[problemOrder.value]);
    //   problem.value = {
    //     ...problem.value, answer: { ...tmpAnswer.value }
    //   }
    // }
}

const init = (newValue) => {
    const vContent = toRaw(newValue);
    answer.value = getAnswer(vContent.data);
    visible.value = (vContent.result && vContent.result.marking !== 'halfWrong') ? true : false;
    // visible.value = true;
    console.log(vContent);
    if (vContent.data.content.property.analysis?.studyTree !== '코어독해') {
        const relationId =
            vContent.data.content.property.problem.relations ? vContent.data.content.property.problem.relations[0].relationId : null;
        if (relationId) {
            getKrtProblem({
                version: '1.00.01', problemId: relationId
                // version: '1.00.01', problemId: 382222
            }, (r) => {
                rProblem.value = {
                    problemNo: 1,
                    problemId: r.content.problemId,
                    etcInfo: null,
                    data: r,
                    studyTime: 0,
                    answer: null,
                    reAnswer: null,
                    result: null,
                }
            });
        }
    }
    setTimeout(() => {
        const Element = document.querySelector('#observerDiv');
        // if (io) {
        //     io.unobserve(Element);
        // }
        const options = {}
        io = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                onIntersection(entry);
            })
        }, options)
        io.observe(Element);
    }, 100);
}

watch(() => props.pContent, (newValue) => {
    console.log('watch !!!!! ');
    visible.value = false;
    init(newValue);
})

onMounted(() => {
    console.log('mount !!!!! ');
    init(props.pContent);
})

onUnmounted(() => {
    console.log('onUnmounted !!!!! ');
    visible.value = false;
})

// 스크롤 하단시 액션
const onIntersection = (entry) => {
    console.log(' onIntersection !!!!!!!!!!!!!!!000');
    console.log(entry);
    intersection.value = entry;
    if (entry.isIntersecting) {
        emit('onIntersection');
    }
    // io.disconnect();
}

</script>

<style lang="scss" scoped>
.answer-box {
    padding: 20px;
    margin: 20px;
    background: #eeeeee;
    border-radius: 10px;
}

::v-deep .q-dialog__inner {
    justify-content: end;
}

.q-dialog__inner :deep {
    justify-content: end;
}
</style>
