	<div class="flx-center">
		<div class="login-box">
			<!-- <div class="login-left"> -->
			<div class="login-form">
				<div style="color:#00A2E0; font-size: 2.8vmin; font-weight: 700; text-align: center;">
					독해 트레이닝
				</div>
				<div style="color:$primary ;font-size: 2.8vmin; font-weight: 700; text-align: center;">
