					<q-input outlined class="icon-none" v-model="name" lazy-rules placeholder="아이디"
						:rules="[val => val && val.length > 0 || '아이디를 입력 해주세요.']" :onFocus="onChange">
						<!-- <q-input outlined class="icon-none" v-model="name" lazy-rules placeholder="아이디" :onFocus="onChange"> -->
						<!-- <template v-slot:prepend>
							<q-icon name="person" />
						</template> -->
					</q-input>
					<q-input outlined class="icon-none" type="password" v-model="pwd" lazy-rules placeholder="비밀번호"
						:rules="[val => val && val.length > 0 || '비밀번호를 입력해 주세요.']" :onFocus="onChange">
						<!-- <q-input outlined class="icon-none" type="password" v-model="pwd" lazy-rules placeholder="비밀번호"
						:onFocus="onChange"> -->
						<!-- <template v-slot:prepend>
							<q-icon name="lock" />
						</template> -->
					</q-input>
					<!-- <q-input outlined v-model="pwd" label="비밀번호" :type="isPwd ? 'password' : 'text'" hint="" :onFocus="onChange"
							lazy-rules :rules="[val => val && val.length > 0 || '비밀번호를 입력해 주세요.']">
							<template v-slot:prepend>
