					<!-- <div style="text-align: center;color: #c10015;font-weight: 500; margin-top: 0px; height: 21px;">
						{{ msg }}
					</div> -->
					<div style="color: #999999;">
						<div class="login-btn" style="text-align: center;">
							<q-btn v-if="check" unelevated color="primary" @click="onSubmit" type="submit" label="로그인"
								size="lg" style="width: 100%; margin: 0px 0px 2.5vmin;" />
							<q-btn v-if="!check" unelevated color="grey-4" text-color="grey-7" type="submit" label="로그인"
								size="lg" style="width: 100%; margin: 0px 0px 2.5vmin;" />
							<span style="font-size: 1.3vmin;">
								로그인을 위한 아이디/비밀번호 생성은 선생님께 문의하세요.
							</span>
						</div>
						<!-- <router-link to="/diagnostic-register">
							<q-btn flat color="primary" label="진단평가" style="width: 100%; " />
						</router-link> -->
					</div>
				</q-form>
				<q-btn unelevated color="primary" @click="() => { passwordModal = true }" type="submit" label="test"
					size="lg" style="width: 100%; margin: 0px 0px 20px;" />
