					<div class="login-icon-box">
						<img src="@/assets/images/login_icon.png" />
					</div>
				</div>
			</div> -->
		</div>
	</div>

	<NewModalLayer :visible="passwordModal" title="비밀번호 변경 안내" @update:visible="(v) => { passwordModal = v }">
		<div style="padding: 0 2.8vmin 1.8vmin;">
			<div style="font-size: 2.1vmin; font-weight: 400;text-align: center;">회원님은 동일한 비밀번호를<br />
				<b>90일 이상 사용하고 계십니다.</b>
			</div>
			<div style="font-size: 1.5vmin;font-weight: 400;margin: 2vmin 0 3vmin;text-align: center;">
				대교 KReaD독해 서비스는 회원님의 개인 정보를 안전하게 보호하고 개인 정보 도용으로 인한 피해를 예방하고자
				주기적으로 비밀번호 변경을 안내하고 있습니다.
			</div>
			<q-form ref="loginFormRef" class="q-gutter-md" @submit="onSubmitPassWord">
				<q-input outlined class="icon-none" type="password" v-model="oldPwd" lazy-rules
					:rules="[val => checkPassword(val) || '비밀번호는 5~20자 의 영문,숫자,특수문자 조합으로 입력해주세요.']"
					placeholder="현재 비밀번호 입력" />
				<q-input outlined class="icon-none" type="password" v-model="newPwd" lazy-rules
					:rules="[val => checkPassword(val) || '비밀번호는 5~20자 의 영문,숫자,특수문자 조합으로 입력해주세요.']"
					placeholder="새 비밀번호 입력" />
				<q-input outlined class="icon-none" type="password" v-model="chkPwd" lazy-rules
					:rules="[val => val === newPwd || '비밀번호가 다릅니다.', val => checkPassword(val) || '비밀번호는 5~20자 의 영문,숫자,특수문자 조합으로 입력해주세요.']"
					placeholder="새 비밀번호 확인" />
			</q-form>
			<div style="display: flex; width: 100%; justify-content: space-between; margin-top: 3.75vmin;">
				<q-btn outline color="primary" label="다음에 변경" @keydown.enter="onCancelPassWord"
					@click="onCancelPassWord"
					style="width: 49%; font-size: 1.5vmin; border-radius: 1vmin;padding: 2vmin 0;" />
				<q-btn color="primary" unelevated label="변경" @keydown.enter="onSubmitPassWord" @click="onSubmitPassWord"
					style="width: 49%; font-size: 1.5vmin;border-radius: 1vmin; padding: 2vmin 0;" />
			</div>
		</div>
