</template>

<script setup name="login">
import { useQuasar } from 'quasar'
import { ref, reactive, onMounted, watch } from "vue";
import { useRouter } from "vue-router";
import { loginApi } from "@/api/modules/login"
import { GlobalStore } from "@/store";
import { initDynamicRouter } from "@/routers/modules/dynamicRouter";
import NewModalLayer from '@/components/layouts/layer/newModalLayer.vue';
import { checkPassword } from '@/helpers/utils';

const globalStore = GlobalStore();

const $q = useQuasar()

const router = useRouter();
const name = ref(null);
const pwd = ref(null);
const oldPwd = ref(null);
const newPwd = ref(null);
const chkPwd = ref(null);
const check = ref(false);
const loginFormRef = ref(null)
const msg = ref('');
const isPwd = ref(true);
const loading = ref(false);
const passwordModal = ref(false);


watch([pwd, name], (newpwd, newname) => {
	console.log('watch');
	if (pwd.value && name.value) {
		check.value = true;
	} else {
		check.value = false;
	}
});

const onSubmit = () => {
	if (!loading.value) {
		loginFormRef.value.validate().then(success => {
			if (success) {
				loading.value = true;
				// @ts-ignore
				const saveCallback = async (rs) => {
					if (rs?.status === 200) {
						await globalStore.setToken(rs.data.data.accessToken);
						await globalStore.setReToken(rs.data.data.refreshToken);
						globalStore.getUserInfo();
						// await initDynamicRouter();
						router.push("/learninghome");
						// $q.notify({
						// 	color: 'green-4',
						// 	textColor: 'white',
						// 	icon: 'cloud_done',
						// 	message: '로그인되었습니다.'
						// })
					} else {
						// msg.value = '아이디/비밀번호를 확인해 주세요.';
						console.log(rs.data)
						$q.dialog({
							title: '확인',
							message: rs?.data?.message,
							// ok: {
							//     push: true,
							// },
							// cancel: {
							//     push: true,
							//     color: 'negative'
							// },
							// cancel: true,
							persistent: true
						})
					}
					loading.value = false;
				}
				loginApi({ loginId: name.value, password: pwd.value, forceLogin: 'Y' }, saveCallback);
			}
			else {

				// $q.notify({
				// 	color: 'red-4',
				// 	textColor: 'white',
				// 	icon: 'cloud_done',
				// 	message: 'Submitted'
				// })
				// oh no, user has filled in
				// at least one invalid value
			}
		})
	}
}

// TODO: 비밀번호 변경 API
const onSubmitPassWord = () => {
	passwordModal.value = false;
}
const onCancelPassWord = () => {
	passwordModal.value = false;

}

const onReset = () => {
	name.value = null
	pwd.value = null
}

const onChange = () => {
	msg.value = '';
}

</script>

