		background-position: center;
		background-size: cover;
	}

	.login-right {
		width: 40%;
		background: linear-gradient(180deg, #326BB8 0%, #76B3DE 100%);

		.login-icon-box {
			width: 100%;
			height: 100%;
			left: -100px;
			position: absolute;

			// width: 300px;
