<template>
    <q-header reveal elevated class="bg-white text-black">
        <q-toolbar>
            <div class="row" style="width: 100%;">
                <div class="col-4" style="display: flex; position: relative; align-items: center;">
                    <q-icon name="img:/assets/images/icon/icon_home.svg" size="20px" :onclick="onClickHome" />
                    <div class="header_lesson q-px-sm"><b>수능모의평가</b></div>
                </div>
                <div class="col-4" style="text-align: center;">
                    <div style="display: inline-flex;align-items: center;">
                        풀이시간 <b style="margin-left: 10px; font-size: 20px; color: #00A2E0;">{{
                                getTimeStringSeconds(timerSec)
                        }}</b>
                    </div>
                </div>
                <div class="col-4" style="display: flex;position: relative;align-items: center; ">
                    <div style="text-align: end; width: 100%;">
                        <q-btn outline :icon-right="`${isMenuOpen === true ? 'expand_less' : 'expand_more'}`" size="md"
                            label="문항번호 보기">
                            <q-menu transition-show="flip-right" transition-hide="flip-left" anchor="bottom middle"
                                self="top middle" @before-show="() => { isMenuOpen = true }"
                                @before-hide="() => { isMenuOpen = false }">
                                <div class="header_nav">
                                    <div class="q-pb-sm">
                                        <b>문항 번호를 눌러 이동해보세요.</b>
                                    </div>
                                    <!-- <q-separator /> -->
                                    <div class="row">
                                        <template v-for="(index) in 34">
                                            <div class="item-nomal"
                                                style="width: 10%; display: grid; justify-content: center;align-items: center; padding: 10px; cursor: pointer;">
                                                <!-- <div :class="[Number(index) > 9 ? (headerInfo[index]?.answer ? 'item-num1 checked' : 'item-num1') : (headerInfo[index]?.answer ? 'item-num checked' : 'item-num')]"
                                                    :onClick="() => { onClick(index) }"> -->
                                                <div :class="getClassName(index)" :onClick="() => { onClick(index) }">
                                                    {{ index }}
                                                </div>
                                                <!-- <div
                                                    style="justify-content:center;display: flex; text-align: center; margin-top: 5px;">
                                                    <div class="checked_small">
                                                        <q-icon v-if="true" name="radio_button_unchecked" size="1em"
                                                            class='checkedicon' />
                                                            <q-icon v-if="false" name="close" size="1em" class='checkedicon' />
                                                        </div>
                                                    </div> -->
                                            </div>
                                            <q-separator v-if="index % 10 === 0" style="height: 1px;width: 100%;" />
                                        </template>
                                    </div>

                                </div>
                            </q-menu>
                        </q-btn>
                    </div>
                </div>
            </div>
        </q-toolbar>
    </q-header>
</template>

<script setup>
import { ref, watch, onMounted, onUnmounted } from 'vue';
import { useRouter } from "vue-router";
import { DialogStore } from "@/store/modules/dialog";
import { StudyStore } from "@/store/modules/study";
import { headerEtcCd02, headerEtcCd01, studyTree } from "@/helpers/qbankParser/qbankMeta";
import { getTimeStringSeconds } from "@/helpers/utils"

const isMenuOpen = ref(false);
const timerSec = ref(0);
const headerInfo = ref(0);
const problemOrder = ref(1);
let secInterval = null;

const router = useRouter();
const store = DialogStore();
const studyStore = StudyStore();

const checkConfirm = (val) => {
    val && router.push('/learninghome');
}

const props = defineProps({
    'timerEnable': false,
    'headerInfo': {},
    'problemOrder': 1
}
);

const emit = defineEmits(['nextStep']);

const disableCheck = (passage, sTree) => {
    if (studyStore.process.examIdx < passage) {
        return true;
    } else {
        if (studyStore.process.studyTree < sTree) {
            return true;
        } else {
            return false;
        }
    }
}

const onClickHome = () => {
    store.openConfirmDialog({
        message: '학습을 중단하고 홈으로 이동하시겠습니까?',
        cancel: '닫기',
        ok: '확인',
        callback: checkConfirm,
    });
}

const onClick = (num) => {
    emit('nextStep', false, num);
}

watch(() => props.timerEnable, (newValue) => {
    clearInterval(secInterval);
    if (newValue) {
        // todo: 기존 측정시간 timeKey 에 해당하는 store에 저장
        // oldV
        // timerSec.value = Number(newV.sec);
        secInterval = setInterval(() => { timerSec.value = (timerSec.value) + 1; }, 1000);
    } else {

    }
});
watch(() => props.headerInfo, (newValue) => {
    console.log('!!!!!!!!!!!!!!!!!!!!');
    console.log(newValue);
    headerInfo.value = newValue;
});
watch(() => props.problemOrder, (newValue) => {
    console.log('!!!!!!!!!!!!!!!!!!!!');
    console.log(newValue);
    problemOrder.value = newValue;
});

onMounted(() => {
    // console.log('onMounted ~~!!!!');
    // console.log(props.sec);
    // timerSec.value = Number(props.sec);
    // secInterval = setInterval(() => { timerSec.value += 1; }, 1000);

})

onUnmounted(() => {
    secInterval && clearInterval(secInterval);
})

const getClassName = (num) => {
    let rsName = 'item';
    if (headerInfo.value[num]?.result) {
        rsName += ('-' + (headerInfo.value[num]?.result?.marking === 'right' ? 'right' : 'wrong'));
        if (headerInfo[num]?.done) {
            rsName += ('-' + 'done');
        }
    } else {
        rsName += '-num';
        if (headerInfo.value[num]?.answer) {
            rsName += (' ' + 'checked');
        }
        if (num === problemOrder.value) {
            rsName += (' ' + 'selected');
        }
    }
    return rsName;
}

</script>

<style scoped lang="scss">
.header {
    &_lesson {}

    &_nav {
        padding: 20px;
        width: 600px;
    }
}

::v-deep .item {
    &-nomal {
        // background-color: #efefef;
        border-radius: 10px;

        .item-num {
            border-radius: 50%;
            border: 1px solid #BDBDBD;
            color: #BDBDBD;
            width: 25px;
            height: 25px;
            text-align: center;
        }

        .item-num1 {
            border-radius: 20px;
            border: 1px solid #BDBDBD;
            color: #BDBDBD;
            padding: 0px 4px 2px;
        }

        .item-right {
            border: 2px solid #118AF1;
            color: black;
            font-weight: 500;
            border-radius: 50%;
            width: 25px;
            height: 25px;
            text-align: center;

            &-done {
                font-weight: 500;
                background-color: #118AF1;
                color: white;
                border-radius: 50%;
                width: 25px;
                height: 25px;
                text-align: center;
            }
        }

        .item-wrong {
            background-image: url('@/assets/images/symbols/x.png');
            background-size: 50%;
            background-repeat: no-repeat;
            background-position-x: center;
            background-position-y: center;
            border: 2px solid #EF2B2A;
            color: black;
            border-radius: 50%;
            width: 25px;
            height: 25px;
            text-align: center;

            &-done {
                font-weight: 500;
                background-color: #EF2B2A;
                color: white;
                border-radius: 50%;
                width: 25px;
                height: 25px;
                text-align: center;
            }
        }

        .checked {
            background-color: #BDBDBD;
            color: white;
        }

        .selected {
            background-color: #000000;
            color: white;
        }

    }

    &-select {
        // background-color: #efefef;
        border-radius: 10px;

        .item-num {
            border-radius: 20px;
            border: 1px solid #BDBDBD;
            background-color: #BDBDBD;
            color: white;
            padding: 0px 8px 2px;
        }

        .item-num1 {
            border-radius: 20px;
            border: 1px solid #BDBDBD;
            background-color: #BDBDBD;
            color: white;
            padding: 0px 4px 2px;
        }
    }


    &-correct {
        // background-color: #d7eaff;
        border-radius: 10px;

        .item-num {
            border: 1px solid #118AF1;
            background-color: #118AF1;
            color: white;
            border-radius: 20px;
            padding: 0px 8px 2px;
        }

        .item-num1 {
            border: 1px solid #118AF1;
            background-color: #118AF1;
            color: white;
            border-radius: 20px;
            padding: 0px 4px 2px;
        }
    }

    &-wrong {
        // background-color: #ffc8d6;
        border-radius: 10px;

        .item-correct_num {
            border: 1px solid #fc6a8f;
            background-color: #fc6a8f;
            border-radius: 20px;
            padding: 0px 8px 2px;
        }

        .item-correct_num1 {
            border: 1px solid #fc6a8f;
            background-color: #fc6a8f;
            border-radius: 20px;
            padding: 0px 4px 2px;
        }
    }
}

.checked_small {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #bbbbbb60;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $primary;

    .checkedicon {
        color: #fff;
    }
}
</style>
