import service from '@/api';
import { GlobalStore } from '@/store';


export const loginApi = async (params, callback) => {
    const globalStore = GlobalStore();
    // const { data } = await service.post(`/api/user/login`, { params });
    await service({
        url: '/api/user/login',
        method: 'post',
        data: params,
    })
        .then((e) => {
            return callback(e);
        })
        .catch((e) => {
            return callback(e);
        });
};

//////////////////--------진단평가---------/////////////////
/**
 * 진단평가 사용자등록
 * @param {*} params 
 * @param {*} callback 
 * @returns token, version
 */
export const postDiagnosticRegister = async (params, callback) => {
    await service({
        url: '/api/preview/diagnostic/register',
        method: 'post',
        data: params,
    }).then((e) => {
        console.log('--postDiagnosticRegister-DATA:', e);
        return callback(e);
    }).catch((e) => {
        console.error('--postDiagnosticRegister-ERR:', e);
        return callback(e);
    });
};

/**
 * 진단평가 결과 제출
 * @param {*} params 
 * @param {*} callback 
 */
export const postDiagnosticSubmit = async (params, callback) => {
    await service({
        url: '/api/preview/diagnostic/submit',
        method: 'post',
        data: params,
    })
        .then((e) => {
            console.log('--postDiagnosticSubmit-DATA:', e);
            return callback(e);
        })
        .catch((e) => {
            console.error('--postDiagnosticSubmit-ERR:', e);
            return callback(e);
        });
};


export const getUserInfo = async (callback) => {
    // const { data } = await service.get(`/api/lesson/start`);
    const { data } = await service({
        url: '/api/user/info',
        method: 'get',
        // data: params,
    });
    if (data && (data?.statusCode === 200 || data?.statusCode === '200'))
        return callback(data.data);
    return null;
};

