<template>
    <div id="templateMulti" class="template multi" ref="trayTemp" style="padding: 20px;">

        <!-- <strong v-if="important == true" class="template__important">중요해요</strong> -->
        <!-- <span class="marking" :class="[markingClass, important == true ? 'withImportant' : '']"></span> -->
        <h6 class="template__title">
            <div style="display: inline-flex;">
                <AnswerMarking :marking-class="props.pContent?.result?.marking" style="top:45px" />
                <div class="template__no"> {{ props.pContent?.problemNo }}. </div>
                <q-chip color="teal" text-color="white">사실적추론</q-chip>
                <q-chip>기본 문제 <span style="color: red;">O</span></q-chip>
                <q-chip>기본문제</q-chip>
            </div>
            <div>
                <span v-html="question"></span>
            </div>
        </h6>
        <section class="direction">
            <!-- <button v-if="audioSrc" type="button" class="btn__play" @click="playAudio('C')">음원 재생</button> -->
            <audio id="cAudio" src=""></audio>
            <!-- 이미지 -->
            <div v-if="directionImg && !directionText" class="direction__frame">
                <figure class="direction__thumb">
                    <img :src="directionImg" class="direction__img" :alt="directionImg" />
                </figure>
            </div>
            <!-- 텍스트 -->
            <div v-if="!directionImg && directionText" class="direction__frame">
                <p class="direction__textbox" v-html="directionText"
                    :style="[textAlign == 'left' ? 'text-align: left' : 'text-align: center']"></p>
            </div>
            <!-- 글상자 -->
            <div v-if="directionTextArea" class="direction__frame">
                <p class="direction__textbox" v-html="directionTextArea"
                    :style="[textAreaAlign == 'left' ? 'text-align: left' : 'text-align: center']"></p>
            </div>
            <!-- 동영상 -->
            <div v-if="videoSrc" class="direction__video">
                <video ref="mov" :src="videoSrc" controls />
            </div>
            <!-- 이미지 + 텍스트 -->
            <div v-if="directionImg && directionText">
                <div class="direction__frame">
                    <figure class="direction__thumb">
                        <img :src="directionImg" class="direction__img" :alt="directionImg" />
                    </figure>
                </div>
                <span class="direction__text" v-html="directionText"
                    :style="[textAlign == 'left' ? 'text-align: left' : 'text-align: center']" />
            </div>
        </section>
        <section id="sectionChoice" class="distracter" :class="disableResolving">
            <div v-if="distracterChoiceText" class="checkbox__wrap" :class="setLayout">
                <!-- <label v-for="(opt, index) in distracterChoiceText" :key="`choiceList_${index}`" class="checkbox" v-touch="true"> -->
                <label v-for="(opt, index) in distracterChoiceText" :key="`choiceList_${index}`" class="checkbox">
                    <input :disabled="showGrade" :type="answerList.length == 1 ? 'radio' : 'checkbox'"
                        :name="answerList.length == 1 ? 'multiChoiceList' : ''" :value="opt.numChar"
                        :true-value="opt.numChar" @click="updateCheckedVal(opt.numChar, index)"
                        :id="`choiceList_${opt.numChar}`" :ref="`choiceList_${index}`" />
                    <div class="checkbox__label ">
                        <div style="display: inline-flex;">
                            <div class="card-item">
                                <q-avatar outline text-color="white"
                                    style="background-color: lightgray; font-size: size(16); ">{{
                                            index + 1
                                    }}</q-avatar>
                            </div>
                            <div class="card-item" v-html="opt.text" />
                        </div>
                    </div>

                </label>
            </div>
            <div v-if="distracterChoiceImg" class="checkbox__wrap" :class="setLayout">
                <label v-for="(opt, index) in distracterChoiceImg" :key="`choiceList_${index}`" class="checkbox"
                    v-touch="">
                    <input :disabled="showGrade" :type="answerList.length == 1 ? 'radio' : 'checkbox'"
                        :name="answerList.length == 1 ? 'multiChoiceList' : ''" :value="opt.numChar"
                        :true-value="opt.numChar" @click="updateCheckedVal(opt.numChar, index)"
                        :id="`choiceList_${opt.numChar}`" :ref="`choiceList_${index}`" />
                    <span class="checkbox__label" :ref="opt.numChar">
                        <figure class="direction__thumb">
                            <img :src="opt.image" class="direction__img" :alt="opt.image" />
                        </figure>
                    </span>
                </label>
            </div>
        </section>
    </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref, defineProps, watch, isProxy, toRaw } from 'vue';
import dataToParse from "@/helpers/qbankParser/dataParse";
import { styleTags } from "@/helpers/qbankParser/styleTags";
import { useQuasar } from 'quasar';
import AnswerMarking from './answerMarking.vue';

const important = ref(false);
const markingClass = ref('');
const problemNo = ref('');
const question = ref('');
const audioSrc = ref('');
const directionImg = ref('');
const directionText = ref('');
const textAlign = ref('');
const videoSrc = ref('');
const directionTextArea = ref('');
const distracterChoiceText = ref([]);
const showGrade = ref(false);
const answerList = ref([]);
const distracterChoiceImg = ref([]);
const tempDirectionText = ref([]);
const tempDirectionPath = ref([]);
const tempDirectionTextArea = ref([]);
const answerObjList = ref([]);
const answerDetailList = ref([]);
const checkedLimit = ref(1);
const choiceArrange = ref([]);
const hintText = ref('');
let explainText = '';
let tempTexts = [];
let answerCount = 0;
let checkedArr = [];
let isCheckedAnswer = false;
const qbankAssetUrl = process.env.VUE_APP_DS_QBANK_ASSET_URL;
let notif = null;
let notiAnswer = null;
let notiType = 'first'; // first, last,  check


const emit = defineEmits(['setCheckedAnswer', 'onChangeProblems', 'setProblemResult', 'setInputAnswer']);
const $q = useQuasar();

const props = defineProps({
    'pContent': {
        default: {}
    },
    'answerNoti': false,
});

const setLayout = () => {
    return '';
};

const disableResolving = () => {
    return {
        // notAllowedChecked: this.allowedChecked == false,
    };
};

const onChangeAnswer = (v) => {
    emit('setInputAnswer', v.target.id, v.target.value)
}

/** 제목 설정 - problem > question > texts의 0번째가 제목 */
const setQuestion = (text) => {
    let content = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'char') {
                content += dataToParse.transferStyleTag(value.content);
            }
        });
    }
    question.value = content;
}

/** 문제 설정 - problem > question > texts의 첫번째가 문제(이미지) */
const setDirectionImage = (text) => {
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'image') {
                directionImg = qbankAssetUrl + value.path;
            } else {
                setDirectionImage(value);
            }
        });
    }
}
/** 지문 설정(텍스트 + 이미지) */
const setTempDirectionPath = (text) => {
    if (text.elements != null) {
        // <CHAR><IMAGE /></CHAR>
        const elements = text.elements;
        for (const element of elements) {
            Object.entries(element).forEach(([key, value]) => {
                if (key == 'image') {
                    tempDirectionPath.push(value.path);
                } else {
                    setTempDirectionPath(value);
                }
            });
        }
    } else {
        // <IMAGE/>
        tempDirectionPath.push(text.path);
    }
}

/** 문제 설정 - problem > question > texts > text의 content 조회 */
let inputboxIds = 1;
const getDirectionTextContent = (text) => {
    let content = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'char') {
                console.log(value);
                console.log(value.content);
                if (value.content.indexOf('INPUTBOX') > -1) {
                    content += dataToParse.transferStyleTag(value.content).replace(/<(INPUTBOX)/g, `<q-input class='input_answers' id="answer_${inputboxIds}" `);
                    inputboxIds += 1;
                } else {
                    content += dataToParse.transferStyleTag(value.content);
                }
            } else {
                getDirectionTextContent(value);
            }
        });
    }
    if (content != '') {
        tempDirectionText.value.push(content);
    }
}

const setDirectionText = (text) => {
    if (text.align != null) {
        textAlign.value = text.align;
    }
    getDirectionTextContent(text);
    console.log(tempDirectionText.value);
    console.log(directionText.value);
    // <TEXT><CHAR></CHAR></TEXT><TEXT><CHAR></CHAR></TEXT>... 형식이 존재하기 때문에 분기처
    if (directionText.value == '') {
        directionText.value = (tempDirectionText.value).join('<br>');
    } else {
        directionText.value = directionText.value + '<br>' + (tempDirectionText.value).join('<br>');
        console.log(directionText.value);
    }
    tempDirectionText.value = [];
}

const setDirectionTextArea = (text) => {
    let content = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'char') {
                let tempContent = dataToParse.transferStyleTag(value.content);
                if (tempContent.indexOf('IMAGE') != -1) {
                    tempContent = tempContent.replace(/IMAGE/gi, 'img class="textbox_inputImg"').replace(/path/gi, 'src');
                    setTempDirectionPath(value);
                    let paths = tempDirectionPath;
                    for (const path of paths) {
                        tempContent = tempContent.replace(path, qbankAssetUrl + path);
                    }
                }
                content += tempContent;
            } else {
                setDirectionTextArea(value);
            }
        });
    }
    if (content != '') {
        let styleAttribute = '';

        let tmpTextAlign = text.align;
        let textIndent = text.textIndent;
        let marginLeft = text.marginLeft;
        let marginRight = text.marginRight;

        if (tmpTextAlign != null) {
            styleAttribute += 'text-align:' + tmpTextAlign + ';';
        }
        if (textIndent != 0) {
            styleAttribute += 'text-indent:' + textIndent + 'px;';
        }
        if (marginLeft != 0) {
            styleAttribute += 'margin-left:' + marginLeft + 'px;';
        }
        if (marginRight != 0) {
            styleAttribute += 'margin-right:' + marginRight + 'px;';
        }

        content = '<div style=' + styleAttribute + '>' + content + '</div>';

        tempDirectionTextArea.push(content);
    }
}

/** content > problem > question 파싱 */
const parsingQuestion = (question, etcCd04) => {
    console.log('parsingQuestion question')
    console.log(question)
    console.log(etcCd04)
    const texts = question.texts;
    if (texts != null) {
        for (const key in texts) {
            // 첫번째 texts는 제목, 두번째 texts는
            if (key == 0) {
                setQuestion(texts[key]);
            } else {
                if (etcCd04 == '01' || etcCd04 == '05') {
                    // 01:이미지, 05:이미지+텍스트
                    let content = texts[key].content;
                    if (content.indexOf('IMAGE') != -1) {
                        let tempElements = texts[key].elements;
                        for (const tempElement of tempElements) {
                            Object.entries(tempElement).forEach(([tempKey, tempValue]) => {
                                // <IMAGE>태그를 <CHAR>태그로 감싼 형식이 존재하기 때문에 분기처리
                                if (tempKey == 'image') {
                                    directionImg = qbankAssetUrl + tempValue.path;
                                } else {
                                    setDirectionImage(tempValue);
                                }
                            });
                        }
                    } else {
                        setDirectionText(texts[key]);
                    }
                } else if (etcCd04 == '02') {
                    // 02:텍스트
                    setDirectionText(texts[key]);
                } else if (etcCd04 == '03') {
                    // 03:글상자
                    let content = texts[key].content;
                    if (content.indexOf('TEXTBOX') != -1) {
                        let tempElements = texts[key].elements;
                        for (const tempElement of tempElements) {
                            Object.entries(tempElement).forEach(([tempKey, tempValue]) => {
                                if (tempKey == 'textBox') {
                                    tempDirectionPath = [];
                                    setDirectionTextArea(tempValue);
                                }
                            });
                        }

                        directionTextArea = tempDirectionTextArea.join('');
                        tempDirectionTextArea = [];
                    } else {
                        setDirectionText(texts[key]);
                    }
                } else if (etcCd04 == '04') {
                    // 04:동영상(안내 문구 이미지가 존재하기 때문에 <IMAGE>태그는 제외)
                    let content = texts[key].content;
                    if (content.indexOf('IMAGE') == -1) {
                        setDirectionText(texts[key]);
                    }
                } else {
                    setDirectionText(texts[key]);

                }
            }
        }


    }
}
/** 선지 설정 - problem > choice > item > texts의 영번째가 선지(Image) */
const setChoiceImage = (text, num, numChar) => {
    let content = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'image') {
                content = { num: num, numChar: numChar, image: qbankAssetUrl + value.path };
            } else {
                setChoiceImage(value, num, numChar);
            }
        });
    }
    if (content != '') {
        distracterChoiceImg.value.push(content);
    }
}

/** 정답 설정 - solution > answer > detail */
const setAnswer = (answer) => {
    let answerObj = {};
    const detail = answer.detail;
    if (detail != null) {
        for (const tempDetail of detail) {
            // ①, ②, ③, ④, ⑤
            answerObj = { ord: tempDetail.ord, answer: tempDetail.answer };
            answerObjList.value.push(answerObj);
            answerDetailList.value.push(tempDetail.answer);
        }
    }
    console.log('setAnswer!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    console.log(answerObjList.value);
    console.log(answerDetailList.value);
    answerList.value = answerDetailList.value;
}

/** content > solution > explain 파싱 */
const parsingExplain = (explain) => {
    const texts = explain.texts;
    if (texts != null) {
        for (const key in texts) {
            setExplainText(texts[key]);
        }
    }
}

/** 설명(explain) 설정 */
const setExplainText = (text) => {
    getTextContent(text);
    if (explainText == '') {
        explainText = tempTexts.join('<br>');
    } else {
        explainText = explainText + '<br>' + tempTexts.join('<br>');
    }
    tempTexts = [];
}

/** 힌트(hint) 설정 */
const setHintText = (text) => {
    getTextContent(text);
    if (hintText.value == '') {
        hintText.value = tempTexts.join('<br>');
    } else {
        hintText.value = hintText.value + '<br>' + tempTexts.join('<br>');
    }
    tempTexts = [];
}

/** text의 content 조회 */
const getTextContent = (text) => {
    let content = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'char') {
                content += dataToParse.transferStyleTag(value.content);
            } else {
                getTextContent(value);
            }
        });
    }
    if (content != '') {
        tempTexts.push(content);
    }
}

/** problem > choice 파싱 */
const parsingChoice = (choice) => {
    const items = choice.items;
    for (const key in items) {
        const num = items[key].num;
        const numChar = items[key].numChar;
        const texts = items[key].texts;
        if (texts.length > 0) {
            for (const key in texts) {
                const content = texts[key].content;
                if (content.indexOf('IMAGE') != -1) {
                    // 선지가 이미지인 경우가 존재(문항코드 : 62061)
                    setChoiceImage(texts[key], num, numChar);
                } else {
                    setChoiceText(texts[key], num, numChar);
                }
            }
        } else {
            // 문제가 오디오일 때 선지의 내용이 빈칸이 경우가 존재(문항코드 : 26332, 26956, 35992)
            let content = { num: num, numChar: numChar, text: '' };
            distracterChoiceText.value.push(content);
        }
    }

    // 선지배열 코드 추출
    choiceArrange.value = choice.array;
}

/** content > solution > hint 파싱 */
const parsingHint = (explain) => {
    const texts = explain.texts;
    if (texts != null) {
        for (const key in texts) {
            setHintText(texts[key]);
        }
    }
}

/** 선지 설정 - problem > choice > item > texts의 영번째가 선지(Text) */
const setChoiceText = (text, num, numChar) => {
    let content = {};
    let tempContent = '';
    const elements = text.elements;
    for (const element of elements) {
        Object.entries(element).forEach(([key, value]) => {
            if (key == 'char') {
                tempContent += dataToParse.transferStyleTag(value.content.replace(/\s/gi, ' '));
                content = { num: num, numChar: numChar, text: tempContent };
            }
        });
    }

    // <ITEM><TEXT></TEXT><TEXT></TEXT>...</ITEM> 형식이 존재하기 때문에 num, numChar을 체크
    let isDistracterText = false;
    let tempKey = '';
    for (const key in distracterChoiceText.value) {
        let tempNum = distracterChoiceText.value[key].num;
        let tempNumChar = distracterChoiceText.value[key].numChar;
        if (tempNum == num && tempNumChar == numChar) {
            isDistracterText = true;
            tempKey = key;
            break;
        }
    }

    if (isDistracterText) {
        distracterChoiceText.value[tempKey].text = distracterChoiceText.value[tempKey].text + ' ' + tempContent;
    } else {
        distracterChoiceText.value.push(content);
    }
}

const parsingContent = (vContent) => {
    const content = vContent;
    console.log(content);
    for (const key in content) {
        if (key == 'problem') {
            const testYn = content[key].testYn; // 채점 가능
            const listen = content[key].listen; // 듣기 파일
            const question = content[key].question;
            const choice = content[key].choice;
            const etc = content[key].etc;
            const etcCd04 = etc.etcCd04; // 문항 형태 변형
            const etcCd06 = etc.etcCd06; // 음원 재생 방법(01:수동재생, 02~04:자동재생)
            const etcCd07 = etc.etcCd07; // 트레이 여부
            const etcTxt04 = etc.etcTxt04; // 선지 선택 개수(2, 3)
            const etcCd02 = etc.etcCd02; // 문제 중요도

            // if (etcCd07 != 'Y') {
            //     this.isTray = false;
            //     // this.setIsTrayVal(false);
            // } else {
            //     // this.setIsTrayVal(true);
            // }

            if (etcTxt04 != '') {
                checkedLimit.value = etcTxt04;
            } else {
                checkedLimit.value = 1;
            }

            if (question != null) {
                // if (testYn != '') {
                //     testYn = testYn;
                // }
                if (listen != '') {
                    if (etcCd04 == '04') {
                        videoSrc.value = qbankAssetUrl + listen;
                    } else {
                        audioSrc.value = qbankAssetUrl + listen;
                        // if (etcCd06 == '01') {
                        //     audioType = 'M'; // 수동
                        // } else {
                        //     audioType = 'A'; // 자동
                        //     audioLimit = parseInt(etcCd06) - 1; // 02:재생 1회, 03:재생 2회, 04:재생 3회
                        // }
                    }
                }
                parsingQuestion(question, etcCd04);
            }
            if (choice != null) {
                parsingChoice(choice);
            }

            if (etcCd02 == 'Y') {
                important.value = true;
            }
        } else if (key == 'solution') {
            const answer = content[key].answer;
            const explain = content[key].explain;
            const hint = content[key].hint;
            if (answer != null) {
                setAnswer(answer);
                // this.setAnswerList(this.answerObjList.map(v => v.answer));
                answerCount = answerObjList.value.length;
                // this.setDetailAnswer(this.answerDetailList);
            }
            if (explain != null) {
                parsingExplain(explain);
                // this.setAnswerDesc(this.explainText);
            }
            if (hint != null) {
                parsingHint(hint);
                // this.setHintData(this.hintText);
            }
        }
    }
}




const updateCheckedVal = (val, idx) => {
    let multiple = answerList.value.length > 1;
    let tempCheckedArr = [];
    if (multiple) {
        let tempCheckedLimit = checkedLimit.value - 1;
        if (tempCheckedLimit > 0) {
            let index = checkedArr.findIndex(v => v.value === val);
            if (index == -1) {
                if (tempCheckedLimit < checkedArr.length) {
                    let checkedValue = checkedArr[0].value;
                    document.getElementById('choiceList_' + checkedValue).checked = false;
                    document.getElementById('choiceList_' + checkedValue).nextSibling.classList.remove('correct');
                    document.getElementById('choiceList_' + checkedValue).nextSibling.classList.remove('wrong');
                    checkedArr.splice(0, 1);
                }
            }
        }

        let index = checkedArr.findIndex(v => v.value === val);
        if (index > -1) {
            checkedArr.splice(index, 1);
            let inputNode = document.querySelectorAll('input');
            let inputArr = Array.prototype.slice.call(inputNode);
            let target = inputArr.filter(v => v.value != checkedArr);

            target.forEach(el => {
                if (val == el.value) {
                    document.getElementById('choiceList_' + el.value).nextSibling.classList.remove('correct');
                    document.getElementById('choiceList_' + el.value).nextSibling.classList.remove('wrong');
                }
            });
        } else {
            checkedArr.push({
                index: idx,
                value: val,
            });
        }
        checkedArr.map(item => {
            tempCheckedArr.push(item.value);
        });
        tempCheckedArr.sort();
    } else {
        tempCheckedArr.push(val);
        const checkList = document.querySelectorAll('input:checked');
        checkList.forEach(el => {
            el.nextSibling.classList.remove('correct');
            el.nextSibling.classList.remove('wrong');
        });
    }
    notiType = props.pContent?.problemNo === 1 ? 'first' : props.pContent?.problemNo === 4 ? 'last' : '';
    if (checkedLimit.value == tempCheckedArr.length) {
        isCheckedAnswer = true;
        console.log(tempCheckedArr);
        // this.setCheckedAnswer(tempCheckedArr);
        let tmpAnswerCnt = 0;
        tempCheckedArr.map(i => {
            if (answerDetailList.value.includes(i)) {
                tmpAnswerCnt++;
            }
        })
        console.log(tmpAnswerCnt);

        emit('setCheckedAnswer', tempCheckedArr, (tmpAnswerCnt === answerDetailList.value.length));
        if (notif === null) {
            notif = $q.notify({
                group: false,
                timeout: 0,
                message: '',
                // color: 'secondary',
                classes: 'force-inline',
                position: 'bottom-right',
                multiLine: true,
                actions:
                    notiType === 'check' ? [{ label: '정답확인', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'check'); } }] :
                        notiType === 'last' ? [{ label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } }] :
                            notiType === 'first' ? [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }] : [
                                { label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } },
                                { label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }
                            ]
            })
        }
        if (notiAnswer === null && props.pContent?.problemNo === 4) {
            notiAnswer = $q.notify({
                group: false,
                timeout: 0,
                message: '',
                multiLine: true,
                color: 'secondary',
                classes: 'force-inlinie',
                position: 'bottom-right',
                actions:
                    [{ label: '정답확인', color: 'yellow', handler: () => { emit('setProblemResult', 'check'); } }]
            });
        }
    } else {
        if (isCheckedAnswer) {
            console.log(tempCheckedArr);
            let tmpAnswerCnt = 0;
            tempCheckedArr.map(i => {
                if (answerDetailList.value.includes(i)) {
                    tmpAnswerCnt++;
                }
            })
            emit('setCheckedAnswer', tempCheckedArr, (tmpAnswerCnt === answerDetailList.value.length));
            // onChangeTab
            // this.setCheckedAnswer(tempCheckedArr);
            if (notif === null) {
                notif = $q.notify({
                    group: false,
                    timeout: 0,
                    message: '',
                    multiLine: true,
                    // color: 'secondary',
                    classes: 'force-inlinie',
                    position: 'bottom-right',
                    actions:
                        notiType === 'check' ? [{ label: '정답확인', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'check'); } }] :
                            notiType === 'last' ? [{ label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } }] :
                                notiType === 'first' ? [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }] : [
                                    { label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } },
                                    { label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }
                                ]
                });
            }
            if (notiAnswer === null && props.pContent?.problemNo === 4) {
                notiAnswer = $q.notify({
                    group: false,
                    timeout: 0,
                    message: '',
                    multiLine: true,
                    color: 'secondary',
                    classes: 'force-inlinie',
                    position: 'bottom-right',
                    actions:
                        [{ label: '정답확인', color: 'yellow', handler: () => { emit('setProblemResult', 'check'); } }]
                });
            }
        }
    }
}

watch(() => props.pContent, (newValue) => {
    // const questionHeader = JSON.parse(JSON.stringify(newValue));
    console.log('!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!');
    const vContent = toRaw(newValue);
    console.log(vContent);
    parsingContent(vContent.question.content);
    if (vContent.answer && vContent.answer.length > 0) {
        vContent.answer.map(i => {
            console.log('onMounted');
            console.log(i);
            console.log(document.getElementById('choiceList_' + i));
            setTimeout(() => {
                document.getElementById('choiceList_' + i).checked = true;
            }, 100);
        })
    }

})

onMounted(() => {
    const vContent = toRaw(props.pContent);
    console.log(vContent);
    parsingContent(vContent.question.content);
    if (vContent.answer && vContent.answer.length > 0) {
        vContent.answer.map(i => {
            console.log('onMounted');
            console.log(i);
            console.log(document.getElementById('choiceList_' + i));
            setTimeout(() => {
                document.getElementById('choiceList_' + i).checked = true;
            }, 100);
        })
        notiType = props.pContent?.problemNo === 1 ? 'first' : props.pContent?.problemNo === 4 ? 'last' : '';
        if (notif === null) {
            notif = $q.notify({
                group: false,
                timeout: 0,
                message: '',
                multiLine: true,
                // color: 'secondary',
                classes: 'force-inlinie',
                position: 'bottom-right',
                actions:
                    notiType === 'check' ? [{ label: '정답확인', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'check'); } }] :
                        notiType === 'last' ? [{ label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } }] :
                            notiType === 'first' ? [{ label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }] : [
                                { label: '이전문제', icon: 'arrow_back_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'prev'); } },
                                { label: '다음문제', iconRight: 'arrow_forward_ios', color: 'yellow', handler: () => { emit('onChangeProblems', 'next'); } }
                            ]
            })
        }
        if (notiAnswer === null && props.pContent?.problemNo === 4) {
            notiAnswer = $q.notify({
                group: false,
                timeout: 0,
                message: '',
                multiLine: true,
                color: 'secondary',
                classes: 'force-inlinie',
                position: 'bottom-right',
                actions:
                    [{ label: '정답확인', color: 'yellow', handler: () => { emit('setProblemResult', 'check'); } }]
            });
        }
    }

    if (inputboxIds > 1) {
        setTimeout(() => {
            const item = document.querySelectorAll('.input_answers');
            item.forEach((target) => {
                target.removeEventListener('keydown', onChangeAnswer);
                target.addEventListener('keydown', onChangeAnswer)
            })

            // const els = document.getElementsByClassName('input_answers');
            // console.log('############################################################3');
            // console.log(els);
            // console.log(els.length);
            // if (els.length > 0) {
            //     console.log(els[0]);
            //     // els.removeEventListener('onchange', emit('setInputAnswer', els));
            //     els[0].addEventListener('change', onChangeAnswer);
            // }
        }, 500)
    }
})

onUnmounted(() => {
    // 
    if (notif) {
        notif();
        notif = null
    }
    if (notiAnswer) {
        notiAnswer();
        notiAnswer = null
    }
})

</script>

<style lang="scss" scoped>
.template {
    &__no {
        margin: 0px 13px;
    }
}

::v-deep input {
    // width: 500px;
    // height: 32px;
    // font-size: 15px;
    border: 1px solid rgb(233, 233, 233);
    border-radius: 5px;
    outline: none;
    margin: 0px 5px;
    text-align: center;
}

::v-deep p {
    margin: 0px;
}


.radio {
    &__wrap {
        grid-template-columns: repeat(2, size(120));
        grid-template-rows: repeat(2, size(120));
    }
}

.direction__frame {
    border: 1px solid;
    padding: 10px;
    font-size: size(12);
}

.distracter {
    margin-top: size(16);
    counter-reset: number;

    .checkbox {
        display: block;
        position: relative;
        width: 100%;
        min-height: size(55);
        margin-bottom: size(20);
        background: white;
        border-radius: size(10);
        border-bottom: size(5) solid #eef3f5;
        box-shadow: 0 size(1) size(5) 0 rgba(64, 87, 1, 0.15);
        font-size: size(17);
        font-weight: 500;

        &__wrap {
            &.arrange2 {
                display: flex;
                flex-wrap: wrap;
                margin: size(-10);

                .checkbox {
                    width: calc(100% / 2 - #{size(20)});
                    margin: size(10);
                }
            }

            &.arrange3 {
                display: flex;
                flex-wrap: wrap;
                margin: size(-10);

                .checkbox {
                    width: calc(100% / 3 - #{size(20)});
                    margin: size(10);
                }
            }

            &.horizontal {
                display: flex;
                margin: size(-10);

                .checkbox {
                    margin: size(10);
                }
            }

            .card-item {
                margin-right: 10px;
                @include center-element
            }
        }

        &__label {
            // display: block;
            display: inline-flex;
            align-items: center;
            position: relative;
            width: 100%;
            // padding: size(16) size(10) size(16) size(32);
            padding: size(16) size(10) size(16) size(10);
            min-height: size(55);
            line-height: 1.31;
            background: white;
            border-radius: size(10);

            &:before {
                // counter-increment: number;
                // content: counter(number);
                display: inline-flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                top: size(16);
                left: size(10);
                width: size(14);
                height: size(14);
                margin-right: size(5);
                border: size(1) solid #000;
                border-radius: 100%;
                font-size: size(12);

                &+u {
                    margin-left: 0;
                }
            }
        }

        input {
            display: none;

            &:checked {
                &+.checkbox__label {
                    box-sizing: border-box;

                    &:after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%;
                        border: size(2.5) solid #3a3a3a;
                        position: absolute;
                        left: 0;
                        top: 0;
                        box-sizing: border-box;
                        border-radius: 9px;
                    }

                    &.correct {
                        border-color: #00a7ff;
                        color: #00a7ff;

                        &:before,
                        &:after {
                            border-color: #00a7ff;
                            color: #00a7ff;
                        }

                        ::v-deep u {
                            color: #00a7ff;
                        }
                    }

                    &.wrong {
                        border-color: #ff0000;
                        color: #ff0000;

                        &:before,
                        &:after {
                            border-color: #ff0000;
                            color: #ff0000;
                        }

                        ::v-deep u {
                            color: #ff0000;
                        }
                    }
                }
            }
        }
    }
}

.template {
    .direction {
        &__thumb {
            padding: size(12.5) 0;
            border: none;
        }

        &__textbox {
            line-height: 1.4;

            ::v-deep>div {
                min-height: size(17);
            }
        }
    }
}

::v-deep .textbox_inputImg {
    vertical-align: middle;
}

.disableTouch {
