<template>
	<div style="width: 100%;height: 100%; position: relative;">
		<div v-if="onLoad" style="width: 100%;height: 100%;">
			<video ref="video" poster="" class="" id="video_player" controls="controls"
				style="width: 100%;height: 100%;">
				<source :src="props.videoSrc" />
			</video>
		</div>
		<div :hidden="!isStop" style="position: absolute;top: 0px;width:100%;height:100%;background-color: #0000006b;">
			<div style="position: absolute;top: 45%;left: 50%; margin-left: -48px; margin-top: -48px;">
				<q-btn round color="#6f7376" :icon="!isEnd ? 'play_arrow' : 'replay'" style="background-color: #6f7376;"
					size="32px" @click="onplay" />
			</div>
			<div style="position: absolute;top: 80%;right: 10px;  ">
				<q-btn rounded dense color="primary"
					:style="'padding: 0 20px;' + (!isEnd ? 'background-color: #6f7376!important;' : '')" size="12px">{{
		isEnd ?
			'개념이해완료'
			:
			'선생님의 영상강의를 통해 개념을 확인해 보세요.'
					}}</q-btn>
			</div>
		</div>
	</div>
	<!-- <q-btn round color="#6f7376" size="32px" @click="onPip" label="pip" /> -->
</template>

<script setup>
import { ref, reactive, onMounted, watch } from "vue";

const isStop = ref(null);
const isEnd = ref(null);
const onLoad = ref(false);
let video;

const emit = defineEmits(['setVideoStatus']);

const props = defineProps({
	'videoSrc': '',
	'videoTime': 10,
});

const onplay = () => {
	if (isStop.value) {
		video.play();
	} else {
		video.pause();
	}
	isStop.value = !isStop.value;
}
const onPip = () => {
	video.requestPictureInPicture()
}


const init = (p) => {
	var supposedCurrentTime = p.videoTime;
	// const video = ref(null)
	video = document.getElementById('video_player');
	isStop.value = true;
	isEnd.value = false;
	console.log(video);
	// video.currentTime = 6;
	const timeupdate = () => {
		if (!video.seeking) {
			supposedCurrentTime = supposedCurrentTime > video.currentTime ? supposedCurrentTime : video.currentTime;
		}
	}
	const seeking = () => {
		// var delta = video.currentTime - supposedCurrentTime;
		// if (Math.abs(delta) > 0.01) {
		// 	console.log("Seeking is disabled");
		// 	video.currentTime = supposedCurrentTime;
		// }

		if (video.currentTime > supposedCurrentTime) {
			video.currentTime = supposedCurrentTime;
		}
	}
	const ended = () => {
		// 동영상 시청종료시 프로세스
		isEnd.value = true
		isStop.value = true;
		emit('setVideoStatus', true);
	}
	video.removeEventListener('timeupdate', timeupdate);
	video.removeEventListener('seeking', seeking);
	video.removeEventListener('ended', ended);
	video.addEventListener('timeupdate', timeupdate);
	video.addEventListener('seeking', seeking);
	video.addEventListener('ended', ended);

}

onMounted(() => {
	setTimeout(() => {
		onLoad.value = true;
		init(props);
	}, 100)
})

watch(() => props, (newValue) => {
	init(props);
})

</script>

